.banner2 {
   padding: 20px 0 28px;

   &__body {
      background: #cfe4f2;
      border-radius: 10px;
      padding: 46px 5px 55px 10px;
      display: flex;
   }

   &__img {
      max-width: 482px;
      width: 100%;
      padding-bottom: 32%;
      min-height: 200px;
      margin-right: 15px;

      & img {
         object-fit: contain;
      }
   }

   &__content {
      flex: 1 1 auto;
   }

   &__title {
      margin-bottom: 15px;
      font-weight: 600;
      font-size: 40px;
      line-height: 45 /40 * 100%;
   }

   &__cities {
      font-size: 18px;
      line-height: 24 /18 * 100%;
      margin-bottom: 45px;
   }

   &__oplata {
      background: #98c5e2;
      border-radius: 10px;
      font-weight: 600;
      font-size: 22px;
      line-height: 26 /22 * 100%;
      padding: 22px 15px 22px 48px;
      margin-bottom: 20px;
      max-width: 665px;
   }

   &__point-btn {
      margin-bottom: 20px;
   }

   &__sale-row {
      display: flex;
      align-items: center;
      margin-bottom: 6px;

      & span {
         font-weight: 600;
         font-size: 35px;
         line-height: 45 /25 * 100%;
         color: #ba0f08;
         margin-right: 15px;
      }

      & p {
         font-weight: 600;
         font-size: 22px;
         line-height: 26 /22 * 100%;
      }
   }

   &__delivery {
      display: flex;
      align-items: center;
   }

   &__delivery-left {
      margin-right: 60px;

      & p {
         font-size: 22px;
         line-height: 26 /22 * 100%;
         margin-bottom: 8px;
      }

      & span {
         font-size: 20px;
         line-height: 22 /20 * 100%;
         max-width: 278px;
         display: block;
      }
   }

   &__btn {
      max-width: 196px;
      width: 100%;

      & a {
         line-height: 55px;
      }
   }
}

@media (max-width: 1200px) {
   .banner2__body {
      padding: 35px 20px 35px 10px;
   }
}

@media (max-width: 1050px) {
   .banner2__img {
      max-width: 354px;
   }

   .banner2__title {
      font-size: 31px;
   }

   .banner2__oplata {
      font-size: 19px;
      padding: 15px 12px 14px 25px;
   }

   .banner2__oplata,
   .banner2__point-btn {
      margin-bottom: 17px;
   }

   .banner2__sale-row p {
      font-size: 21px;
   }

   .banner2__sale-row span {
      font-size: 32px;
   }

   .banner2__delivery-left span {
      font-size: 17px;
      max-width: 278px;
   }

   .banner2__cities {
      margin-bottom: 18px;
   }

   .banner2__delivery-left p {
      font-size: 17px;
   }
}

@media (max-width: 950px) {
   .banner2__body {
      flex-direction: column;
   }

   .banner2__body {
      padding: 25px;
   }

   .banner2__img {
      max-width: 485px;
      margin: 0 auto 30px;
      padding-bottom: 37%;
   }
}

@media (max-width: 550px) {
   .banner2__title {
      font-size: 21px;
   }

   .banner2__cities {
      font-size: 16px;
      margin-bottom: 18px;
   }

   .banner2__oplata,
   .banner2__point-btn {
      margin-bottom: 12px;
   }

   .banner2__delivery-left p {
      font-size: 14px;
   }

   .banner2__btn a {
      line-height: 40px;
   }

   .banner2__sale-row span {
      font-size: 20px;
      margin-right: 10px;
   }

   .banner2__delivery-left span {
      font-size: 14px;
      max-width: 170px;
   }

   .banner2__sale-row p {
      font-size: 17px;
   }

   .banner2__oplata {
      font-size: 17px;
      padding: 15px;
   }

   .banner2__delivery-left {
      margin-right: 10px;
   }

   .banner2__btn {
      margin-left: auto;
      max-width: 142px;
   }

   .banner2__img {
      margin: 0 auto 19px;
   }

   .banner2__body {
      padding: 15px;
   }
}

@media (max-width: 420px) {
   .banner2__delivery {
      flex-wrap: wrap;
   }

   .banner2__btn {
      margin-left: 0;
      margin-top: 15px;
   }
}
