.delivery {
   padding: 20px 0 20px;

   &__head {
      margin-bottom: 36px;
      background: #f2f3f7;
      border-radius: 15px;
      padding: 39px 0 52px;
      text-align: center;
   }

   &__map-wrap {
      width: 100%;
      display: flex;
      justify-content: center;
   }

   &__title {
      margin-bottom: 15px;
      font-weight: bold;
      font-size: 36px;
      line-height: 40 /36 * 100%;
      color: #4b4b4b;
   }

   &__subtitle {
      font-size: 22px;
      line-height: 25 /22 * 100%;
      color: #4b4b4b;
   }

   &__content {
      display: flex;
   }

   &__map {
      max-width: 734px;
      width: 100%;
      min-height: 640px;

      & img {
         opacity: 0.3;
      }
   }

   &__point {
      margin-left: 42px;
   }
}

