   &__point-item4 {
      top: 16%;
      left: 45%;
   }

   &__point-item5 {
      left: 30%;
      top: 28%;
   }

   &__point-item6 {
      left: 39%;
      top: 31%;
   }

   &__point-item7 {
      left: 56%;
      top: 34%;
   }

   &__point-item8 {
      left: 49%;
      top: 40%;
   }

   &__point-item9 {
      left: 75%;
      top: 40%;
   }

   &__point-item10 {
      left: 85%;
      top: 42%;
   }

   &__point-item11 {
      left: 7%;
      top: 47%;
   }

   &__point-item12 {
      left: 28%;
      top: 48%;
   }

   &__point-item13 {
      left: 11%;
      top: 56%;
   }

   &__point-item14 {
      left: 20%;
      top: 56%;
   }

   &__point-item15 {
      left: 7%;
      top: 73%;
   }

   &__point-item16 {
      left: 17%;
      top: 71%;
   }

   &__point-item17 {
      left: 32%;
      top: 73%;
   }

   &__point-item18 {
      left: 44%;
      top: 63%;
   }

   &__point-item19 {
      left: 37%;
      top: 68%;
   }

   &__point-item20 {
      left: 40%;
      top: 58%;
   }

   &__point-item21 {
      left: 62%;
      top: 55%;
   }

   &__point-item22 {
