   }

   &__point-item25 {
      left: 80%;
      top: 67%;
   }
}

.point {
   &__block {
      margin-bottom: 30px;
   }

   &__head {
      position: relative;
   }

   &__allcity-wrap {
      max-height: 300px;

      &::-webkit-scrollbar-track {
         border-radius: 10px;
      }

      overflow: auto;

      &::-webkit-scrollbar {
         width: 4px;
      }

      &::-webkit-scrollbar-thumb {
         border-radius: 10px;
         height: 100px;
         background: #e5e5e5;
      }

      overflow: auto;
   }

   &__allcity-close {
      position: absolute;
      top: 10px;
      right: 10px;
      width: 25px;
      height: 25px;
      cursor: pointer;

      &::after,
      &::before {
         content: "";
         width: 25px;
         height: 1px;
         position: absolute;
         background-color: #000;
         top: 50%;
         transform: translateY(-50%);
      }

      &::before {
         transform: rotate(-45deg);
      }

      &::after {
         transform: rotate(45deg);
      }
   }

   &__allcity {
      max-height: 0px;
      transition: all 0.3s ease 0s;
      opacity: 0;
      visibility: hidden;

      &.active {
         max-height: 500px;
         visibility: visible;
         opacity: 1;
      }

      position: absolute;
      top: calc(100% + 8px);
      left: 0;
      padding: 40px 35px 20px 25px;
      z-index: 10;
      background-color: #fff;
      border-radius: 10px;
      box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);

      & span {
         line-height: 25 /18 * 100%;
         font-size: 18px;
         font-weight: 500;
      }
   }

   &__title {
      font-size: 24px;
      line-height: 24px;
      line-height: 24 /24 * 100%;
      color: #ba0f08;
      margin-bottom: 13px;
   }

   &__city {
      font-size: 18px;
      line-height: 24 /18 * 100%;
   }

   &__all-city {
      margin-left: 24px;
   }

   &__list {
      & ul {
         & li {
            position: relative;
            font-size: 18px;
            line-height: 24 /18 * 100%;
            margin-bottom: 10px;
            padding-left: 22px;

            &::after {
               position: absolute;
               content: "";
               left: 0;
               top: 4px;
               width: 15px;
               height: 16px;
               background-image: url("../img/sprite/ok.svg");
               background-repeat: no-repeat;

               & svg {
                  fill: #ba0f08;
               }
            }

            &:last-child {
               margin-bottom: 0;
            }
         }
      }
   }

   &__more-row {
      display: flex;
      justify-content: space-between;
      align-items: center;
   }

   &__cureer-btn {
      margin-right: 40px;
   }

   &__terminal-btn {
      margin-right: 40px;
   }

   &__bottom {
      margin-top: 45px;

      & a {
         margin-bottom: 15px;
      }
   }
}

.map-point {
   &:hover .map-point__detal {
      opacity: 1;
      visibility: visible;

      overflow: auto;
   }

   background: #ffffff;
   border: 5px solid #ba0f08;
   position: absolute;
   width: 32px;
   height: 32px;
   border-radius: 50%;
   cursor: pointer;

   &__digit {
      font-size: 16px;
      font-weight: 700;
   }

   &__spolers {
      border-bottom: 1px dashed #999;
      padding-bottom: 10px;
      padding-top: 10px;
   }

   &__spolers-head {
      position: relative;
      padding-right: 18px;

      &._active::after {
         transform: translateY(-50%) rotate(180deg);
      }

      &::after {
         position: absolute;
         background-image: url("../img/sprite/arrow-down.svg");
         content: "";
         transition: all 0.3s ease 0s;

         & svg {
            fill: #000;
         }

         top: 50%;
         right: 0;
         transform: translateY(-50%) rotate(0deg);
         background-repeat: no-repeat;
         width: 15px;
         height: 16px;
      }
   }

   &__detal {
      overflow: auto;
      max-height: 187px;
      position: absolute;
      background-color: #fff;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      padding: 15px;
      bottom: 40px;
      left: -32px;
      max-width: 270px;
      width: 100%;
      z-index: 20;
      min-width: 270px;
      font-size: 12px;
      font-weight: 500;
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease 0s;
      z-index: 10;

      &::after {
         content: "";
         position: absolute;
         width: 0;
         height: 0;
         border-left: 10px solid transparent;
         border-right: 10px solid transparent;
         border-top: 10px solid #fff;
         left: 32px;
         bottom: -10px;
      }
   }

   &__info {
      margin-top: 15px;
      margin-bottom: 5px;
      font-size: 14px;
   }

   &__adress {
      display: flex;
      align-items: center;
      margin-top: 15px;

      & span {
         font-weight: 700;
         font-size: 12px;
      }
   }

   &__adress-icon {
      width: 13px;
      height: 13px;
      margin-right: 7px;

      & svg {
         fill: #2e8bc8;
         stroke: #2e8bc8;
      }
   }

   &__city {
      font-size: 16px;
      font-weight: 700;
   }

   &__operator {
      display: flex;
      align-items: center;
      margin-top: 15px;

      & span {
         font-weight: 700;
         font-size: 12px;
      }
   }

   &__operator-icon {
      width: 14px;
      height: 14px;
      margin-right: 7px;

      & svg {
         fill: #2e8bc8;
      }
   }

   &__time {
      display: flex;
      margin-top: 15px;
   }

   &__spolers .map-point__adress {
      margin-top: 0;
   }

   &__time-icon {
      margin-right: 7px;
      width: 14px;
      height: 14px;

      & svg {
         fill: #2e8bc8;
      }
   }

   &__time-row {
      display: flex;
      margin-bottom: 10px;

      &:last-child {
         margin-bottom: 0;
      }
   }

   &__time-days {
      font-size: 12px;
      margin-right: 7px;

      &::after {
         content: ":";
      }
   }

   &__time-line {
      margin-left: 10px;
      margin-right: 10px;
      width: 1px;
      background-color: #999;
   }

   &__time-hours {
      font-size: 12px;
      font-weight: 700;
   }
}

@media (max-width: 1230px) {
   .delivery__map img {
      object-fit: contain;
   }

   .delivery__point {
      margin-left: 13px;
   }
}

@media (max-width: 1190px) {
   .delivery__content {
      flex-wrap: wrap;
      justify-content: center;
   }

   .point__bottom {
      flex-wrap: wrap;
   }

   .point__bottom a {
      text-align: left;
      margin-right: 12px;
      white-space: nowrap;
   }
}

@media (max-width: 1050px) {
   .delivery__map {
      max-width: 70%;
   }

   .delivery__map-wrap {
      padding: 150px 0 80px;
      overflow: auto;
   }
}

@media (max-width: 991px) {
   .delivery__head {
      margin-bottom: 25px;
      padding: 20px 0 32px;
   }

   .delivery__map {
      min-height: auto;
      padding-bottom: 78%;
      padding-bottom: 60%;
   }

   .delivery__title {
      font-size: 31px;
   }

   .delivery__subtitle {
      font-size: 20px;
   }

   .delivery__point {
      margin-left: 0;
      margin-top: 20px;
   }
}

@media (max-width: 768px) {
   .delivery__map {
      min-height: auto;
      padding-bottom: 87%;

      & img {
         object-fit: contain;
      }
   }

   .map-point {
      border: 3px solid #ba0f08;
      width: 25px;
      height: 25px;
   }

   .map-point__digit {
      font-size: 13px;
   }

   .point__bottom {
      display: flex;
      justify-content: space-between;
   }

   .point__cureer-btn,
   .point__terminal-btn {
      margin-right: 0;
   }

   .delivery__map {
      max-width: 100%;
   }
}

@media (max-width: 550px) {
   .map-point {
      width: 20px;
      height: 20px;
      border: 3px solid #ba0f08;
   }

   .map-point__digit {
      font-size: 9px;
   }

   .delivery__map {
      min-width: 290px;
   }

   .point__title {
      font-size: 20px;
      margin-bottom: 8px;
   }

   .point__city,
   .point__list ul li {
      font-size: 16px;
   }

   .point__more-row {
      align-items: flex-end;
   }

   .delivery__subtitle {
      font-size: 16px;
   }

   .delivery__title {
      font-size: 22px;
   }

   .point__block {
      margin-bottom: 24px;
   }
}
