.breadcrumb {
   padding-top: 17px;

   &__list {
      display: flex;
      align-items: center;

      & li {
         position: relative;

         &:hover a {
            color: #ba0f08;
         }

         &:last-child::after {
            display: none;
         }

         &::after {
            content: "/";
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: -8px;
         }

         margin: 0 5px;

         & span {
            font-size: 15px;
            line-height: 20 /15 * 100%;

            color: #000000;
         }

         & a {
            transition: all 0.3s ease 0s;
            font-size: 15px;
            line-height: 20 /15 * 100%;
            color: #4b4b4b;
         }
      }
   }
}

