.foto {
   padding: 20px 0 122px;

   &__title {
      margin-bottom: 10px;
      text-align: center;
   }

   &__subtitle {
      font-size: 22px;
      line-height: 25 /22 * 100%;
      text-align: center;
      margin-bottom: 30px;
   }

   &__gallery {
      display: flex;
   }

   &__img--height {
      height: 100%;
      border-radius: 15px;
      overflow: hidden;
      min-height: 400px;
   }

   &__left {
      flex: 1 0 auto;
      margin-right: 20px;
      max-width: 845px;
      flex-wrap: wrap;
   }

   &__row {
      display: flex;
      flex: 0 0 100%;
      justify-content: space-between;

      &:first-child {
         margin-bottom: 20px;
      }
   }

   &__img {
      flex: 0 0 calc(50% - 10px);
      padding-bottom: 39%;
      width: 100%;
      border-radius: 15px;
      overflow: hidden;
   }

   &__img--big {
      flex: 0 0 100%;
   }

   &__right {
      flex: 0 0 400px;
   }
}

@media (max-width: 991px) {
   .foto__img--height {
      min-height: 200px;
      padding-bottom: 38%;
   }

   .foto__img {
      padding-bottom: 78%;
   }
}

@media (max-width: 768px) {
   .foto__subtitle {
      font-size: 18px;
      margin-bottom: 19px;
   }

   .foto__img {
      margin-bottom: 20px;
   }

   .foto__gallery {
      flex-direction: column;
   }

   .foto__left {
      margin-right: 0;
   }

   .foto {
      padding: 20px 0 42px;
   }

   .foto__img--height {
      padding-bottom: 157%;
   }

   .foto__row:first-child {
      margin-bottom: 0;
   }

   .foto__right {
      flex: 0 0 100%;
   }
}

@media (max-width: 550px) {
   .foto__row {
      flex-direction: column;
   }

   .foto__img {
      margin-bottom: 20px;
   }
}
