.header {
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   background-color: #fff;
   z-index: 15;
   transform: translateY(0px);
   transition: all 0.3s ease 0s;

   &.scroll {
      transform: translateY(-161px);

      &.up {
         transform: translateY(-50px);
      }

      & .header__bottom {
         box-shadow: 0px 2px 8px 0px rgba(42, 47, 51, 0.2);
      }
   }

   &__top {
      padding: 14px 0 15px;
      border-bottom: 1px solid #e5e5e5;
   }

   &__menu {
      & ul {
         display: flex;

         & li {
            margin-right: 30px;

            & a {
               font-size: 16px;
               line-height: 21 /16 * 100%;
               transition: all 0.3s ease 0s;

               &:hover {
                  color: #999;
               }
            }

            &:last-child {
               margin-right: 0;
            }
         }
      }
   }

   &__center {
      padding: 14px 0 32px;
   }

   &__logo {
      max-width: 230px;
      margin-right: 36px;
   }
}

.header-center {
   &__wrapper {
      display: flex;
      justify-content: space-between;
   }

   &__left {
      display: flex;
      align-items: center;
   }

   &__descr {
      & p {
         white-space: nowrap;
         font-size: 16px;
         line-height: 21 /16 * 100%;
      }

      & span {
         font-size: 18px;
         line-height: 24 /18 * 100%;

         color: #ba0f08;
      }
   }

   &__right {
      display: flex;
      align-items: center;
   }

   &__info {
      & span {
         display: block;
         font-size: 16px;
         line-height: 21 /16 * 100%;
      }

      margin-right: 89px;
   }

   &__phone {
      transition: all 0.3s ease 0s;
      display: inline-flex;
      align-items: center;

      &:hover {
         color: #ba0f08;
      }

      & span {
         font-weight: 600;
         font-size: 21px;
         line-height: 28px;
      }
   }

   &__phone-icon {
      width: 17px;
      height: 17px;
      margin-right: 5px;

      & svg {
         fill: #ba0f08;
      }
   }

   &__basket {
      margin-left: 51px;
      position: relative;

      &:hover .basket__wrap {
         visibility: visible;
         opacity: 1;
      }
   }

   &__basket-link {
      position: relative;

      & span {
         position: absolute;
         bottom: 6px;
         right: -11px;
         font-size: 12px;
         background: #ba0f08;
         width: 20px;
         display: inline-flex;
         align-items: center;
         justify-content: center;
         border-radius: 50%;
         height: 20px;
         color: #ffffff;
      }
   }

   &__basket-icon {
      width: 45px;
      height: 45px;

      & svg {
         transition: all 0.3s ease 0s;
         fill: #303030;
      }
   }
}

.basket__wrap {
   position: absolute;
   padding-top: 20px;
   right: 0;
   top: 100%;
   transition: all 0.3s ease 0s;
   opacity: 0;
   visibility: hidden;
   z-index: 5;
}

.basket__content {
   background: #ffffff;
   box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
   border-radius: 15px;
   min-height: 200px;
   width: 472px;

   &--empty {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      font-weight: 600;
      font-size: 24px;
      line-height: 32px;
   }
}

.header {
   &__bottom {
      background-color: #f2f3f7;
   }

   &__bottom-wrapper {
      display: flex;
      align-items: center;
      position: relative;
   }
}

.catalog {
   &__head {
      display: inline-flex;
      align-items: center;
      width: 100%;
      padding: 9px 0 16px 24px;
      max-width: 305px;
      background-color: #fff;

      &:hover span {
         color: #ba0f08;
      }

      & span {
         transition: all 0.3s ease 0s;
         font-size: 20px;
         line-height: 27 /20 * 100%;
      }
   }

   &__icon {
      width: 27px;
      height: 14px;
      margin-right: 5px;

      & svg {
         stroke: #ba0f08;
      }
   }

   &__content {
      padding-right: 11px;

      &::-webkit-scrollbar-track {
         border-radius: 10px;
      }

      max-height: 497px;
      overflow: auto;

      &::-webkit-scrollbar {
         width: 4px;
      }

      &::-webkit-scrollbar-thumb {
         border-radius: 10px;
         height: 100px;
         background: #e5e5e5;
      }
   }

   display: flex;
   transition: all 0.3s ease 0s;
   opacity: 0;
   visibility: hidden;
   pointer-events: none;
   position: absolute;
   top: calc(100% + 3px);
   width: auto;

   box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.03);

   &.active {
      visibility: visible;
      opacity: 1;
      pointer-events: all;
   }

   &__body {
      background-color: #fff;
      width: 100%;
      padding: 0px 7px 24px 13px;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.03);
      flex: 0 0 305px;

      & ul {
         & li {
            cursor: pointer;
            position: relative;
            padding: 12px 10px 14px 10px;
            border-bottom: 1px solid #e5e5e5;
            transition: all 0.3s ease 0s;

            &.active {
               border-bottom: 1px solid #ba0f08;

               & a {
                  color: #ba0f08;
               }

               & svg {
                  fill: #ba0f08;
               }
            }

            & ul {
               display: block;
               position: absolute;
               left: 100%;
               height: 100%;
               max-width: 305px;
            }

            &:hover {
               border-bottom: 1px solid #ba0f08;

               & svg {
                  fill: #ba0f08;
               }

               & > a {
                  color: #ba0f08;
               }
            }

            & a {
               transition: all 0.3s ease 0s;
               font-size: 15px;
            }
         }
      }
   }

   &__arrow {
      width: 12px;
      height: 11px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0;

      & svg {
         transition: all 0.3s ease 0s;
         fill: #303030;
      }
   }
}

.bottom-menu {
   margin-left: 24px;

   & ul {
      display: flex;

      & li {
         margin-right: 60px;
         padding: 16px 0 18px;

         &:last-child {
            margin-right: 0;
         }
      }
   }

   & a {
      transition: all 0.3s ease 0s;
      font-size: 20px;
      display: inline-flex;
      align-items: center;

      &:hover {
         color: #ba0f08;
      }
   }

   &__icon {
      margin-right: 6px;
      width: 18px;
      height: 17px;

      & svg {
         fill: #ba0f08;
      }
   }
}

.catalog {
   &__submenu {
      background-color: #fff;
      width: 100%;
   }

   &.long {
      max-width: 100%;
      width: 100%;
   }

   max-width: 305px;
   box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.03);
}

.submenu {
   &.active {
      display: flex;
   }

   max-width: 332px;
   width: 100%;
   box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.03);
   padding: 22px 22px 24px 44px;
   display: none;

   &__list-link {
      margin-right: 60px;

      &:last-child {
         margin-right: 0;
      }

      & li {
         margin-bottom: 3px;

         &:hover a {
            color: #ba0f08;
         }

         & a {
            transition: all 0.3s ease 0s;
            font-size: 15px;
            line-height: 20 /15 * 100%;
            text-decoration-line: underline;
            color: #2e8bc8;
         }
      }
   }

   &__list {
      padding-right: 30px;

      &::-webkit-scrollbar-track {
         border-radius: 10px;
      }

      max-width: 330px;
      width: 100%;
      max-height: 467px;
      overflow: auto;

      &::-webkit-scrollbar {
         width: 4px;
      }

      &::-webkit-scrollbar-thumb {
         border-radius: 10px;
         height: 100px;
         background: #e5e5e5;
      }

      & li {
         margin-bottom: 10px;

         &:last-child {
            margin-bottom: 0;
         }

         &.active {
            & a {
               color: #ba0f08;
               text-decoration: underline;
            }
         }

         &:hover a {
            color: #ba0f08;
            text-decoration: underline;
         }

         & a {
            font-size: 15px;
            line-height: 20 /15 * 100%;
            transition: all 0.3s ease 0s;
         }
      }
   }

   &__brend {
      max-width: 490px;
      width: 100%;
      left: -2px;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      overflow: hidden;
      max-height: auto;
      display: none;
      padding: 25px;
      background-color: #fff;
   }

   &__two {
      max-width: 305px;

      & .submenu__list-two {
         width: 100%;
      }

      & ul {
         &::-webkit-scrollbar-track {
            border-radius: 10px;
         }

         max-width: 330px;
         width: 100%;
         max-height: 467px;
         overflow: auto;

         &::-webkit-scrollbar {
            width: 4px;
         }

         &::-webkit-scrollbar-thumb {
            border-radius: 10px;
            height: 100px;
            background: #e5e5e5;
         }

         overflow: auto;
         max-height: 467px;
      }

      & li {
         margin-bottom: 10px;

         &:last-child {
            margin-bottom: 0;
         }

         &:hover a {
            color: #ba0f08;
            text-decoration: underline;
         }

         & a {
            font-size: 15px;
            line-height: 20 /15 * 100%;
            transition: all 0.3s ease 0s;
         }
      }
   }

   &__brend-title {
      margin-bottom: 7px;
      font-weight: 600;
      font-size: 18px;
      line-height: 24 /18 * 100%;
   }

   &__brend-row {
      display: flex;
      margin-bottom: 17px;
   }

   &__brend-img {
      max-width: 185px;
      align-self: flex-end;
      margin-right: 9%;
   }

   & img {
      object-fit: contain;
   }
}

.subbrend.active {
   display: flex;
   width: 100%;
}

.basket {
   &__wrap {
   }

   &__content {
      padding: 22px 18px 28px 8px;
   }

   &__items {
      width: 100%;
   }

   &__item {
      margin-bottom: 20px;

      &:last-child {
         margin-bottom: 0;
      }
   }

   &__top {
      display: flex;
      align-items: center;
   }

   &__left {
      display: flex;
      align-items: center;
   }

   &__img {
      width: 129px;
      min-height: 50px;
      padding-bottom: 30%;
      margin-right: 5px;
   }

   &__name {
      max-width: 160px;
      margin-right: 5px;
      font-size: 20px;
      line-height: 24 /20 * 100%;
   }

   &__right {
      display: flex;
      margin-left: auto;
   }

   &__count {
      font-size: 24px;
      line-height: 24 /24 * 100%;
      white-space: nowrap;
      font-weight: 400;
   }

   &__delete {
      margin-left: 10px;
      cursor: pointer;
   }

   &__icon {
      width: 20px;
      height: 23px;

      & svg {
         stroke: #4b4b4b;
         fill: none;
      }
   }

   &__bottom {
      margin-top: 25px;
      display: flex;
      align-items: center;
      padding-left: 32px;
   }

   &__all {
      font-size: 24px;
      margin-right: 5px;
      font-weight: 400;

      & span {
         font-weight: 600;
         margin-left: 7px;
      }
   }

   &__btn {
      max-width: 196px;
      margin-left: auto;
   }
}

@media (max-width: 1170px) {
   .header-center__info {
      margin-right: 22px;
   }

   .submenu__brend {
      max-width: 350px;
   }

   .submenu {
      max-width: 279px;
   }

   .header-center__basket {
      margin-left: 30px;
   }
}

@media (max-width: 1089px) {
   .header-center__descr {
      display: none;
   }

   .header__logo {
      max-width: 220px;
      margin-right: 17px;
   }
}

@media (max-width: 991px) {
   .submenu__brend {
      padding: 12px;
   }

   .submenu__two {
      padding: 16px 13px 16px 20px;
   }

   .submenu__list-link {
      margin-right: 19px;
   }

   .submenu {
      padding: 16px 13px 16px 20px;
   }
}

@media (max-width: 768px) {
   .header__top {
      display: none;
   }

   .basket__wrap {
      right: -75px;
   }

   .header-center__phone {
      display: none;
   }

   .header-center__info > span {
      display: none;
   }

   .header-center__right {
      flex-wrap: wrap;
      justify-content: flex-end;
   }

   .header-center__info {
      flex: 0 0 100%;
      text-align: right;
      margin-bottom: 7px;
      margin-right: 0;
   }

   .menu__icon {
      margin-left: 25px;
   }

   .header-center__basket {
      margin-left: 20px;
   }

   .header__bottom {
      display: none;
   }

   .header__center {
      padding: 8px 0 15px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.2);
   }

   .header-center__wrapper {
      align-items: flex-end;
   }

   .header__logo {
      min-width: 150px;
   }

   .btn {
      font-size: 13px;
      line-height: 35px;
      padding: 0 14px;
   }

   .header.scroll.up {
      transform: translateY(0);
   }

   .header.scroll {
      transform: translateY(0);
   }
}

@media (max-width: 550px) {
   .header__logo {
      max-width: 137px;
      margin-right: 6px;
   }

   .basket__content {
      width: 100%;
   }

   .basket__name {
      max-width: 147px;
      font-size: 14px;
   }

   .basket__all {
      font-size: 15px;
   }

   .basket__img {
      width: 96px;
   }

   .basket__count {
      font-size: 16px;
   }

   .header-center__btn-wrap {
      display: none;
   }

   .header-center__basket-icon {
      width: 38px;
      height: 37px;
   }
}
