.btn {
   background: #ba0f08;
   border-radius: 30px;
   font-weight: 600;
   font-size: 16px;
   line-height: 45px;
   padding: 0 36px;
   display: inline-flex;
   align-items: center;
   width: 100%;
   white-space: nowrap;
   justify-content: center;
   color: #ffffff;
   transition: all 0.3s ease 0s;

   &--transparent {
      &:hover {
         color: #fff;
      }

      color: #ba0f08;
      border: 1px solid #ba0f08;
      background-color: #fff;
   }

   &:hover {
      background: darkred;
   }
}

.title {
   font-size: 36px;
   font-weight: 400;
   line-height: 45 /36 * 100%;
   color: #303030;
}

.btn--blue {
   background: #2e8bc8;

   &:hover {
      background: darkblue;
   }
}

.btn-dotted {
   transition: all 0.3s ease 0s;
   font-size: 18px;
   border-bottom: 1px dotted #ba0f08;
   color: #ba0f08;

   &:hover {
      color: darkred;
   }
}

.btn-link {
   font-size: 18px;
   line-height: 26 /18 * 100%;
   text-decoration: underline;
   color: #ba0f08;
   transition: all 0.3s ease 0s;

   &:hover {
      color: darkred;
   }
}

.slider-btn {
   background: #ffffff;
   box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
   max-width: 48px;
   height: 48px;
   display: inline-flex;
   align-items: center;
   justify-content: center;
   cursor: pointer;
   width: 100%;
   border-radius: 50%;
   z-index: 3;

   &__icon {
      width: 23px;
      height: 18px;

      & svg {
         fill: #ba0f08;
      }
   }
}

.slider-dots {
   display: flex;

   & li {
      margin-right: 14px;
      cursor: pointer;

      &.slick-active button::after {
         position: absolute;
         top: 50%;
         left: 50%;
         transform: translate(-50%, -50%);
         width: 11px;
         height: 11px;
         min-width: 11px;
         min-height: 11px;
         border-radius: 50%;
         background-color: #ba0f08;
         content: "";
      }

      & button {
         transition: all 0.3s ease 0s;
         border-radius: 50%;
         border: 1px solid #4b4b4b;
         width: 18px;
         height: 18px;
         color: transparent;
         background-color: #fff;
         position: relative;

         &:hover {
            border: 1px solid #ba0f08;
         }
      }

      &:last-child {
         margin-right: 0;
      }
   }
}

.form-input {
   width: 100%;

   & input {
      background: #ffffff;
      border-radius: 10px;
      font-weight: 600;
      font-size: 18px;
      color: #4b4b4b;
      border: none;
      padding: 17px 10px 18px 25px;
   }
}

.subtitle {
   font-size: 18px;
   line-height: 26 /18 * 100%;
   color: #4b4b4b;
}

.buttons-buy {
   position: relative;
   width: 100%;

   &__icon {
      width: 28px;
      height: 28px;

      & svg {
         fill: #ffffff;
      }
   }

   &__one-click {
      position: relative;
      width: 100%;
      height: 45px;
      font-size: 16px;
      line-height: 100%;
      display: inline-flex;
      align-items: center;
      display: inline-flex;
      padding: 0px 70px 0px 40px;
      border: 1px solid #ba0f08;
      border-radius: 30px;
      transition: all 0.3s ease 0s;

      &:hover {
         color: #ba0f08;
      }
   }

   &__basket-btn {
      top: 0;
      position: absolute;
      right: 0;
      background: #ba0f08;
      border-radius: 0px 30px 30px 0px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: 100%;
      max-width: 65px;
      transition: all 0.3s ease 0s;

      &:hover {
         background-color: darkred;
      }
   }
}

.card {
   height: 100%;
   background: #ffffff;
   box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
   border-radius: 15px;
   padding: 45px 25px 25px 25px;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   position: relative;
   transition: all 0.3s ease 0s;

   &__add-btn {
      max-width: 222px;
      margin: 0 auto;
   }

   &__slider-dots {
      display: flex;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);

      & li {
         &.slick-active {
            background: #a7daea;
         }

         cursor: pointer;
         border-radius: 50%;
         background: #e5e5e5;
         font-size: 0;
         width: 18px;
         height: 18px;
         margin-right: 7px;

         &:last-child {
            margin-right: 0;
         }
      }
   }

   &.active {
      box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.15);
      transform: translateY(-12px);

      &::after {
         display: block;
      }
   }

   &::after {
      display: inline-block;
      width: 0;
      position: absolute;
      top: calc(100% - 6px);
      left: 50%;
      content: "";
      display: none;
      transform: translateX(-50%);
      height: 0;
      border-style: solid;
      border-width: 0 15px 30px 15px;
      border-color: transparent transparent #fff transparent;
   }

   &__img {
      display: block;
      max-width: 200px;
      width: 100%;
      padding: 0px 0px 55% 0px;
      margin: 0 auto 20px;

      & img {
         object-fit: contain;
      }
   }

   &__descr {
      font-size: 18px;
      transition: all 0.3s ease 0s;

      &:hover {
         color: #ba0f08;
      }
   }

   &__price {
      font-weight: 600;
      font-size: 20px;

      margin: auto 0px 15px 0px;
   }

   &__bottom {
      margin-top: 10px;
   }

   &__buttons {
      margin: 0 auto 9px;
      width: 100%;
      max-width: 255px;
   }

   &__review {
      text-align: center;
      position: relative;
   }

   &__review-btn {
      transition: all 0.3s ease 0s;
      font-size: 18px;
      line-height: 17px;
      text-decoration-line: underline;
      color: #ba0f08;

      &:hover {
         color: darkred;
      }
   }
}

.payment {
   &--margin {
      margin: 35px 0 36px;
   }

   &__body {
      background: #a7daea;
      border-radius: 10px;
      padding: 22px 15px 27px;
      text-align: center;
   }

   &__descr {
      font-size: 18px;
      line-height: 24 /18 * 100%;
   }

   &__title {
      font-weight: 600;
      font-size: 30px;
      margin-bottom: 12px;
   }

   &__btn {
      margin-top: 14px;
   }
}

.select {
   position: relative;
}

.select.is-active .select__body {
   max-height: 400px;
   height: auto;
}

.select.is-active {
   .select__icon {
      transform: scaleY(-1);
   }

   .select__body {
      display: block;
   }
}

.select__header {
   cursor: pointer;
   display: flex;
   align-items: center;
   transition: all 0.4s;
   font-size: 18px;
   line-height: 24 /18 * 100%;
   display: inline-flex;
   align-items: center;
}

.select__current {
   font-size: 18px;
   font-weight: 400;
}

.select__icon {
   transition: all 0.3s ease 0s;
   height: 8px;
   margin-left: 10px;
   width: 8px;
}

.select__body {
   display: none;
   max-height: 0;
   overflow: auto;
   position: absolute;
   top: calc(100% + 6px);
   width: 100%;
   z-index: 5;
   border-top: none;
   box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
   background-color: #fff;
}

.select.is-active {
   .select__current-wrap svg {
      transform: scaleY(-1);
   }
}

.select__item {
   transition: all 0.3s ease 0s;
   font-size: 16px;
   padding: 7px 3px 7px 10px;
   cursor: pointer;

   &.active {
      background-color: #f2f2f2;
      color: #ba0f08;
   }
}

.select__wrapp {
   background-color: #fff;
   width: 100%;
   padding: 5px 0px 0px 0px;
}

.select__item:hover {
   background-color: lightgray;
}

.radiobox {
   display: flex;
   align-items: center;

   &__container {
      display: block;
      position: relative;
      padding-left: 47px;
      cursor: pointer;
      font-weight: 600;
      font-size: 30px;
      line-height: 45 /30 * 100%;
      color: #303030;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;

      & input {
         position: absolute;
         opacity: 0;
         cursor: pointer;
         pointer-events: none;

         &:checked ~ .radiobox__radiomark::after {
            display: block;
         }
      }
   }

   &__container .radiobox__radiomark::after {
      width: 18px;
      content: "";
      height: 18px;
      background-color: #98c5e2;
      border-radius: 50%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
   }

   &__radiomark {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      border: 3px solid #98c5e2;
   }

   &__radiomark::after {
      position: absolute;
      content: "";
      display: none;
   }
}

.quantity {
   max-width: 96px;
   display: flex;
   align-items: center;
   width: 100%;
}

.plus-btn {
   font-size: 30px;
   color: #303030;
}

.input-btn {
   margin: 0 10px;
   font-size: 30px;
   line-height: 45px;
   color: #303030;
   background-color: none;
   background-color: transparent;
   border: none;
}

.minus-btn {
   font-size: 30px;
   font-weight: 600;
   color: #303030;
}
