.faq {
   padding: 75px 0;

   &__title {
      margin-bottom: 35px;
      text-align: center;
   }

   &__content {
      max-width: 853px;
      margin: 0 auto;
   }

   &__item {
      position: relative;
   }

   &__head {
      background-color: #fff;
      padding: 6px 20px 10px 15px;
      display: inline-flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      cursor: pointer;
      border-bottom: 1px solid #979797;
      transition: all 0.3s ease 0s;
      position: relative;
      z-index: 0;

      &._active {
         position: relative;
         z-index: 4;
         transform: translateY(-3px);
         border-bottom: none;
         box-shadow: 0px -12px 15px -6px rgba(0, 0, 0, 0.15);
         border-radius: 10px;

         & .faq__icon {
            transform: rotateX(180deg);
         }
      }

      & span {
         font-size: 20px;
         line-height: 45 /20 * 100%;
      }
   }

   &__icon {
      transition: all 0.3s ease 0s;
      width: 18px;
      height: 15px;
      margin-left: 10px;

      & svg {
         fill: #98c5e2;
      }
   }

   &__body {
      font-size: 16px;
      line-height: 45 /16 * 100%;
      padding: 18px 15px 20px 60px;
      box-shadow: 0px 9px 15px -4px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      background-color: #fff;
      z-index: 2;
      position: relative;
   }
}

@media (max-width: 768px) {
   .faq {
      padding: 40px 0;
   }

   .faq__title {
      margin-bottom: 16px;
   }

   .faq__head span {
      font-size: 18px;
   }

   .faq__body {
      padding: 8px 15px 10px 35px;
      line-height: 180%;
   }

   .faq__head {
      padding: 3px 15px 7px 8px;
   }
}

@media (max-width: 550px) {
   .faq__head span {
      font-size: 15px;
      line-height: 150%;
   }

   .faq__body {
      padding: 7px 10px 8px 20px;
   }

   .faq__icon {
      width: 14px;
      height: 11px;
   }
}
