.high-ath {
   padding: 39px 0 44px;

   &__title {
      text-align: center;
      margin-bottom: 13px;
   }

   &__subtitle {
      text-align: center;
      margin-bottom: 35px;
   }

   &__items {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      margin: -12px -10px;
      box-sizing: border-box;
   }

   &__item {
      flex: 0 0 calc(25% - 20px);
      margin: 12px 10px;
      background: #ffffff;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
      border-radius: 10px;
      padding: 15px 15px 30px 15px;
      min-height: 265px;
      transition: all 0.3s ease 0s;

      &:hover {
         box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.15);
         transform: translateY(-10px);
      }
   }

   &__item-content {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 100%;
   }

   &__img {
      max-width: 200px;
      width: 100%;
      padding: 0px 0px 70% 0px;

      & img {
         max-width: 100%;
         object-fit: contain;
      }
   }

   &__name {
      font-size: 18px;
      line-height: 26 /18 * 100%;
      justify-self: flex-end;
   }
}

@media (max-width: 991px) {
   .high-ath {
      padding: 15px 0 20px;
   }

   .high-ath__item {
      flex: 0 0 calc(50% - 20px);
      min-height: 240px;
   }
}

@media (max-width: 550px) {
   .high-ath__item {
      flex: 0 0 calc(100% - 20px);
      min-height: 240px;
      padding: 10px 10px 15px;
   }
}
