.simulator {
   padding: 87px 0 27px;
   background-color: #a7daea;
   background-image: url("../img/home/bg2.png");
   background-repeat: no-repeat;
   background-size: 100%;
   background-position: center;

   &__body {
      display: flex;
   }

   &__title {
      font-weight: 600;
      font-size: 50px;
      line-height: 67 /50 * 100%;
      color: #161616;
      margin-bottom: 40px;
   }

   &__descr {
      font-weight: 600;
      font-size: 24px;
      line-height: 32 /24 * 100%;
      margin-bottom: 26px;
      max-width: 665px;
   }

   &__link {
      margin-bottom: 50px;

      & a {
         font-size: 20px;
      }
   }

   &__bottom {
      display: flex;
      align-items: center;
   }

   &__prices {
      display: flex;
      align-items: center;
   }

   &__new-price {
      font-weight: 600;
      font-size: 55px;
      line-height: 73 /55 * 100%;
      color: #161616;
   }

   &__old-price {
      font-size: 40px;
      line-height: 53 /40 * 100%;
      margin-left: 15px;
      margin-right: 15px;
      text-decoration: line-through;
   }

   &__btn {
      max-width: 249px;
      width: 100%;

      & a {
         line-height: 63px;
      }
   }

   &__img {
      position: relative;
      left: -50px;
      margin-top: -52px;
      max-width: 527px;
      width: 100%;
      margin-left: -79px;
      padding-bottom: 42%;
   }
}

@media (max-width: 1235px) {
   .simulator__img {
      left: 0;
      margin-top: -52px;

      margin-left: 20px;

      & img {
         object-fit: contain;
      }
   }

   .simulator__title {
      font-size: 38px;

      margin-bottom: 11px;
   }

   .simulator__prices {
      flex-wrap: wrap;
   }

   .simulator__link {
      margin-bottom: 19px;
   }

   .simulator__old-price {
      margin-left: 0;
   }

   .simulator__old-price {
      order: -1;
   }

   .simulator {
      padding: 60px 0 23px;
   }

   .simulator__new-price {
      font-size: 40px;
   }
}

@media (max-width: 991px) {
   .simulator__title {
      font-size: 33px;
   }

   .simulator__descr {
      font-size: 20px;
      margin-bottom: 9px;
   }

   .simulator__new-price {
      font-size: 35px;
   }

   .simulator__old-price {
      font-size: 31px;
   }

   .simulator__btn a {
      line-height: 46px;
   }
}

@media (max-width: 768px) {
   .simulator {
      padding: 35px 0 23px;
   }

   .simulator__title {
      font-size: 26px;
   }

   .simulator__img {
      margin-top: 30px;
      margin-left: auto;
      margin-right: auto;
      padding-bottom: 52%;
   }

   .simulator__btn {
      margin-left: 20px;
   }

   .simulator__body {
      flex-wrap: wrap;
   }
}

@media (max-width: 550px) {
   .simulator__title {
      font-size: 20px;
   }

   .simulator__descr {
      font-size: 16px;
   }

   .simulator__img {
      padding-bottom: 96%;
   }

   .simulator__old-price {
      font-size: 26px;
   }

   .simulator__new-price {
      font-size: 31px;
   }
}
