.footer {
   background: #f2f3f7;

   &__top {
      padding: 37px 0 69px;
      border-bottom: 1px solid #c7c7c7;
   }

   &__text {
      color: #979797;

      & p {
         font-size: 18px;
         line-height: 26 /18 * 100%;
      }
   }

   &__copy {
      padding: 22px 0 25px;
   }

   &__copy-wrapper {
      & span {
         font-size: 18px;
      }
   }
}

.footer-top {
   &__row {
      display: flex;
      justify-content: space-between;

      &:first-child {
         margin-bottom: 47px;
      }
   }

   &__column {
      flex: 0 0 calc(33.33% - 14px);
   }

   &__logo {
      margin-bottom: 24px;
      max-width: 220px;
   }

   &__info {
      max-width: 410px;
   }

   &__phone {
      margin-bottom: 10px;

      & a {
         transition: all 0.3s ease 0s;
         font-size: 26px;

         &:hover {
            color: #ba0f08;
         }
      }
   }

   &__mail {
      margin-bottom: 10px;

      & a {
         font-size: 22px;

         transition: all 0.3s ease 0s;
         display: inline-flex;
         align-items: center;

         &:hover {
            color: #ba0f08;
         }
      }
   }

   &__messenger {
      & p {
         color: #4b4b4b;
      }
   }

   &__links {
      & ul {
         display: flex;
         justify-content: space-between;
         flex-wrap: wrap;

         & li {
            flex: 0 0 45%;
            margin-bottom: 20px;

            & a {
               transition: all 0.3s ease 0s;
               font-size: 20px;

               &:hover {
                  color: #ba0f08;
               }
            }
         }
      }
   }

   &__payments {
      display: flex;
      flex-wrap: wrap;
   }

   &__payment {
      flex: 0 0 33.33%;
      max-width: 150px;
      margin-bottom: 12px;
   }
}

.footer-mail {
   &__icon {
      margin-right: 9px;
      width: 22px;
      height: 22px;

      & svg {
         fill: #ba0f08;
      }
   }
}

.footer-form {
   &__title {
      font-size: 24px;
      line-height: 26 /24 * 100%;
      margin-bottom: 3px;
   }

   &__subtitle {
      font-size: 18px;
      margin-bottom: 25px;
   }

   &__input {
      & input {
         padding: 9px;
         background-color: transparent;
         border: 0;
         font-size: 18px;
         color: #979797;
         border-bottom: 1px solid #c7c7c7;
      }
   }

   &__btn-wrap {
      text-align: center;
      margin-top: 25px;
   }

   &__btn {
      max-width: 333px;
   }
}

@media (max-width: 991px) {
   .footer-top__row:first-child {
      flex-wrap: wrap;
   }

   .footer-top__column {
      flex: 0 0 calc(50% - 14px);
      margin-bottom: 25px;
   }

   .footer-top__column:last-child {
      flex: 0 0 calc(100% - 14px);
      margin-bottom: 0;
   }

   .footer-form__input {
      max-width: 320px;
      margin-right: 20px;
   }

   .footer-top__row {
      flex-wrap: wrap;
   }

   .footer-form__block {
      display: flex;
      align-items: center;
   }

   .footer-form__subtitle {
      margin-bottom: 4px;
   }
}

@media (max-width: 768px) {
   .footer__top {
      padding: 21px 0 43px;
   }

   .footer-form__btn-wrap {
      text-align: left;
   }

   .footer-top__column {
      flex: 0 0 calc(100% - 14px);
   }

   .footer-top__row:first-child {
      margin-bottom: 32px;
   }
}

@media (max-width: 550px) {
   .footer-form__block {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
   }

   .footer-form__title {
      font-size: 22px;
   }

   .footer-form__subtitle {
      font-size: 16px;
      margin-bottom: 15px;
   }

   .footer__text p {
      font-size: 16px;
   }

   .footer-top__links ul li a {
      font-size: 17px;
   }

   .footer-top__payment {
      flex: 0 0 50%;
      max-width: 200px;
      margin-bottom: 12px;
   }

   .footer-top__payment {
      display: inline-flex;
      align-items: center;
   }
}
