.text {
   padding: 50px 0;

   &__head {
      text-align: center;
      margin-bottom: 15px;
   }

   &__title {
      margin-bottom: 15px;
   }

   &__subtitle {
      max-width: 686px;
      margin: 0 auto;
   }

   &__info {
      font-size: 16px;
      line-height: 26 /16 * 100%;
      color: #4b4b4b;
   }
}

@media (max-width: 768px) {
   .text {
      padding: 30px 0;
   }
}

@media (max-width: 550px) {
   .text__info {
      font-size: 14px;
   }

   .text {
      padding: 20px 0;
   }
}
