.choose {
   padding: 50px 0 38px;

   &__head {
      text-align: center;
      margin-bottom: 15px;
   }

   &__title {
      margin-bottom: 5px;
   }

   &__content {
      position: relative;
   }

   &__img {
      z-index: -1;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, calc(-50% + 56px));
      max-width: 587px;
   }

   &__row {
      display: flex;
      justify-content: space-between;

      &:nth-child(2) {
         margin-bottom: 70px;
      }

      &:nth-child(3) {
         padding: 0 49px 0 83px;
         margin-bottom: 60px;
      }
   }

   &__block1 {
      &::after {
         background-image: url("../img/line/01.png");
         top: 18px;
         right: -88px;
         width: 235px;
         height: 51px;
         background-size: contain;
      }
   }

   &__block2 {
      &::after {
         background-image: url("../img/line/02.png");
         top: 13px;
         right: 8px;
         width: 500px;
         background-size: contain;
         height: 100px;
      }
   }

   &__block3 {
      &::after {
         background-image: url("../img/line/03.png");
         top: 24px;
         right: -88px;
         width: 250px;
         background-size: contain;
         height: 70px;
      }
   }

   &__block4 {
      &::after {
         background-image: url("../img/line/04.png");
         top: 22px;
         right: 155px;
         width: 250px;
         background-size: contain;
         height: 70px;
      }
   }

   &__block5 {
      &::after {
         background-image: url("../img/line/05.png");
         top: 22px;
         right: -215px;
         width: 200px;
         background-size: contain;
         height: 70px;
      }
   }

   &__block6 {
      &::after {
         background-image: url("../img/line/06.png");
         top: 22px;
         right: 221px;
         width: 160px;
         background-size: contain;
         height: 70px;
      }
   }

   &__block {
      position: relative;

      &::after {
         position: absolute;
         content: "";
         background-repeat: no-repeat;
      }

      &:first-child {
         text-align: left;
      }

      &:last-child {
         text-align: right;
      }
   }

   &__name {
      font-size: 30px;
      line-height: 45 /30 * 100%;
   }

   &__text {
      max-width: 300px;
      font-size: 16px;
      line-height: 25 /16 * 100%;
   }
}

@media (max-width: 1230px) {
   .choose__img {
      max-width: 528px;
      z-index: 3;
   }

   .choose__row:nth-child(3) {
      padding: 0 29px 0 45px;
      margin-bottom: 60px;
   }

   .choose__block1::after {
      width: 227px;
   }
}

@media (max-width: 1130px) {
   .choose__img {
      position: relative;
      top: 0;
      left: 0;
      transform: translate(0);
      margin: 0 auto 20px;
      max-width: 373px;
   }

   .choose__block::after {
      display: none;
   }

   .choose__row:nth-child(2) {
      margin-bottom: 19px;
   }

   .choose__text {
      max-width: 96%;
   }

   .choose__block:last-child {
      text-align: left;
   }

   .choose__row:nth-child(3) {
      padding: 0;
      margin-bottom: 20px;
   }
}

@media (max-width: 991px) {
   .choose {
      padding: 40px 0 30px;
   }
}

@media (max-width: 768px) {
   .choose__row {
      justify-content: flex-start;
      flex-wrap: wrap;
   }

   .choose__block {
      margin-bottom: 20px;
   }

   .choose__row:nth-child(2) {
      margin-bottom: 0;
   }

   .choose__row:nth-child(3) {
      margin-bottom: 0;
   }

   .choose__name {
      font-size: 25px;
   }

   .choose {
      padding: 30px 0 20px;
   }
}

@media (max-width: 550px) {
   .choose__block {
      margin-bottom: 10px;
   }

   .choose__text {
      font-size: 15px;
   }

   .choose__img {
      max-width: 70%;
   }

   .choose__name {
      font-size: 21px;
   }
}
