.video {
   padding: 0 0 25px;

   &__title {
      margin-bottom: 35px;
   }

   &__items {
      display: flex;
      margin: -10px;
   }

   &__item {
      flex: 0 0 calc(25% - 20px);
      margin: 10px;
   }

   &__content {
      width: 100%;
      min-height: 100px;
      padding-bottom: 65%;
      max-width: 100%;
   }
}

@media (max-width: 991px) {
   .video__item {
      flex: 0 0 calc(50% - 20px);
   }

   .video__items {
      flex-wrap: wrap;
   }
}

@media (max-width: 768px) {
   .quick--relative {
      padding: 11px 0 26px 0px;
   }
}

@media (max-width: 550px) {
   .video__title {
      margin-bottom: 21px;
   }

   .video__item {
      flex: 0 0 calc(100% - 20px);
   }
}
