.review {
   padding: 60px 0 43px;

   &__subtitle {
      font-weight: 600;
      font-size: 20px;
      line-height: 30 /20 * 100%;
      text-align: center;
      margin-bottom: 42px;

      & span {
         color: #ba0f08;
      }
   }
}

.review-tabs {
   &__nav {
      max-width: 846px;
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid #c7c7c7;
      margin: 0 auto 20px;
   }

   &__item {
      font-size: 36px;
      line-height: 45 /36 * 100%;
      padding-bottom: 12px;
      cursor: pointer;

      &._active {
         border-bottom: 1px solid #ba0f08;
      }
   }
}

.review-slider {
   & .slick-list {
      padding: 50px 0 75px !important;
   }

   &__left {
      top: 260px;
      position: absolute;
      left: calc(50% - 274px);
      margin-right: 100px;
   }

   &__right {
      top: 260px;
      position: absolute;
      right: calc(50% - 274px);

      margin-left: 100px;
   }
}

.review-slide {
   transition: all 0.3s ease 0s;
   box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
   border-radius: 15px;
   background: #ffffff;
   margin: 0 10px;
   overflow: hidden;
   width: 100%;

   &.slick-current {
      position: relative;
      z-index: 5;
      transform: scale(1.2) translateY(10px);
   }

   &__img {
      min-height: 283px;
      width: 100%;
      padding-bottom: 40%;
      border-radius: 15px 15px 0px 0px;
      overflow: hidden;
   }

   &__content {
      padding: 18px 40px 20px 26px;
   }

   &__text {
      font-size: 20px;
      line-height: 30 /20 * 100%;
      margin-bottom: 20px;
   }

   &__name {
      font-weight: 600;
      font-size: 24px;
      line-height: 30 /24 * 100%;
      margin-bottom: 20px;
   }

   &__video {
      & .yt-lazyload {
         padding-bottom: 68.5%;
      }
   }

   &__audio-body {
      height: 283px;
      overflow: auto;
      padding: 39px 48px 57px 42px;
      border-bottom: 1px solid #c7c7c7;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
   }

   &__adress {
      font-size: 18px;
      line-height: 30 /18 * 100%;
      color: #4b4b4b;
      margin-bottom: 5px;
   }

   &__date {
      font-size: 18px;
      line-height: 30 /18 * 100%;
      color: #4b4b4b;
   }

   &__audio-wrap {
      margin-top: auto;
   }

   & audio {
      width: 100%;
   }
}

.review-slider--audio {
   & .review-slide__text {
      max-width: 200px;
   }

   & .review-slide.slick-current {
      & .review-slide__text {
         max-width: 100%;
      }
   }
}

.review-slide__video .yt-lazyload {
   max-width: 100%;
}

@media (max-width: 991px) {
   .review-tabs__item {
      font-size: 26px;
   }
}

@media (max-width: 768px) {
   .review-slide__text {
      font-size: 17px;
      margin-bottom: 10px;
   }

   .review-slide__name {
      font-size: 18px;

      margin-bottom: 10px;
   }
}

@media (max-width: 550px) {
   .review-slide.slick-current {
      transform: scale(1);
   }

   .review-slider .slick-list {
      padding: 10px 0 !important;
   }

   .review-slider__left {
      top: 154px;
      position: absolute;
      left: -16px;
      margin-right: 0;
   }

   .review__subtitle {
      font-size: 17px;

      margin-bottom: 21px;
   }

   .review-slider__right {
      top: 154px;
      position: absolute;
      right: -16px;
      margin-left: 0;
   }

   .review-slide__img {
      min-height: 193px;
   }

   .review-tabs__item {
      font-size: 17px;
   }

   .review-slide__video .yt-lazyload {
      padding-bottom: 56.5%;
   }
}
