.complect {
   background-color: #f2f3f7;
   padding: 61px 0 120px;

   &__title {
      text-align: center;
      margin-bottom: 24px;
   }
}

.complect-slider {
   margin: 0 -10px;

   &__dots {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: -30px;
   }

   & .slick-track {
      display: flex;
   }
}

.complect-slide {
   margin: 0 10px;
   padding: 15px 0;
   height: auto;
}

.complect-card {
   background: #ffffff;
   box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
   border-radius: 15px;
   padding: 0px 0px 38px 0px;
   overflow: hidden;
   min-height: 300px;
   height: 100%;
   display: flex;
   flex-direction: column;
   justify-content: space-between;

   &__img-box {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 30px;
   }

   &__img {
      padding: 0 0 79%;
      min-height: 150px;
      width: 100%;

      & img {
         object-fit: contain;
      }
   }

   &__title {
      align-self: flex-start;
      margin-bottom: 17px;
      font-weight: 600;
      font-size: 22px;
   }

   &__content {
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: 290px;
      margin: 0 auto;
   }

   &__list {
      width: 100%;

      & li {
         margin-bottom: 10px;
         display: flex;
         justify-content: space-between;

         &:last-child {
            margin-bottom: 0;
         }
      }
   }

   &__name {
      font-size: 18px;
   }

   &__price {
      font-size: 18px;
   }

   &__price-row {
      margin-top: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 25px;
   }

   &__new-price {
      margin-right: 25px;
      font-weight: 600;
      font-size: 24px;
   }

   &__old-price {
      font-size: 20px;
      position: relative;

      &::after {
         content: "";
         position: absolute;
         height: 1px;
         background-color: #4b4b4b;
         top: 50%;
         left: 50%;
         transform: translate(-50%, -50%);
         width: 120%;
      }
   }
}

@media (max-width: 991px) {
   .complect {
      padding: 48px 0 67px;
   }
}

@media (max-width: 768px) {
   .complect-card__img {
      padding: 0 0 46%;
   }

   .complect-card__content {
      max-width: 430px;
   }
}

@media (max-width: 550px) {
   .complect-card__content {
      padding: 0 10px;
      max-width: 100%;
   }

   .complect-card__img {
      padding: 0px 0 65%;
   }

   .complect .discount-slider__left,
   .complect .discount-slider__right {
      top: 30%;
   }

   .complect-card {
      padding: 0px 0 26px;
   }

   .complect {
      padding: 30px 0 60px;
   }
}
