.yt-lazyload {
   width: 100%;
   position: relative !important;
   overflow: hidden;
   cursor: pointer;
   -moz-user-select: none;
   -ms-user-select: none;
   user-select: none;
   min-height: 200px;
   max-width: 491px;
   padding-bottom: 25%;
}

.yt-lazyload-wrap {
   position: absolute;
   top: 0;
   right: 0;
   bottom: 0;
   left: 0;
   padding: inherit;
}

.video-fon {
   background-color: rgba(0, 0, 0, 0.4);
   width: 100%;
   height: 100%;
   top: 0;
   left: 0;
   position: absolute;
}

.yt-lazyload::after {
   background-color: rgba(0, 0, 0, 0.4);
   width: 100%;
   height: 100%;
   top: 0;
   position: absolute;
   left: 0;
}

.yt-lazyload-playbtn {
   position: absolute;
   top: 50%;
   background-image: url("../img/sprite/play.svg");
   background-repeat: no-repeat;
   background-size: cover;
   width: 71px;
   height: 71px;
   left: 50%;
   transform: translate(-50%, -50%);
}

.yt-lazyload iframe {
   width: 100% !important;
   height: 100% !important;
   position: absolute;
   top: 0;
   left: 0;
   z-index: 3;
   border: 0;
}
