.mobile-menu {
   position: fixed;
   top: 0;
   left: 0;
   z-index: 20;
   width: 100%;
   background-color: #fff;
   transform: translateX(-100%);
   display: none;
   transition: all 0.3s ease 0s;
   min-height: 100vh;

   &._active {
      transform: translateX(0px);
   }
}

.mobile-menu {
   &::after {
      content: "";
      background-color: #fff;
      width: 100%;
      top: 0;
      left: 0;
      height: 60px;
      position: absolute;
   }

   &__spollers {
      & a {
         width: 100%;
         position: relative;

         &::after,
         &::before {
            position: absolute;
            content: "";
            height: 2px;
            width: 20px;
            background-color: #303030;
            top: 50%;
            transform: translateY(-50%);
            right: 0;
            transition: all 0.3s ease 0s;
         }

         &._active::after {
            transform: rotate(0deg);
         }

         &::after {
            transform: rotate(90deg);
         }
      }
   }

   &__gift-icon {
      width: 20px;
      height: 20px;
      margin-right: 9px;

      & svg {
         fill: #ba0f08;
      }
   }

   &__links {
      margin: 30px 0;

      & li {
         margin-bottom: 17px;

         &:hover span {
            color: #ba0f08;
         }

         &:last-child {
            margin-bottom: 0;
         }

         & a {
            display: inline-flex;
            align-items: center;

            & span {
               transition: all 0.3s ease 0s;
               font-weight: 700;
               font-size: 20px;
            }
         }
      }
   }

   &__content {
      overflow: auto;
      max-height: 100vh;
      padding: 80px 25px 60px 35px;
   }

   & li {
      margin-bottom: 15px;

      & > a {
         transition: all 0.3s ease 0s;

         &:hover {
            color: #ba0f08;
         }

         transition: all 0.3s ease 0s;
         font-size: 20px;
         font-weight: 500;
      }
   }

   &__first-list {
      & li {
         margin-bottom: 20px;

         &:last-child {
            margin-bottom: 0;
         }
      }
   }

   &__two-list {
      padding-left: 30px;
      margin: 20px 0;

      & li {
         & a {
            transition: all 0.3s ease 0s;

            &:hover {
               color: #ba0f08;
            }

            font-size: 18px;

            &::after,
            &::before {
               width: 15px;
            }
         }

         margin-bottom: 18px;
      }
   }

   &__third-list {
      padding-left: 20px;
      margin: 15px 0;

      & li {
         & a {
            font-size: 16px;
            transition: all 0.3s ease 0s;

            &:hover {
               color: #ba0f08;
            }

            &::after,
            &::before {
               display: none;
            }
         }

         margin-bottom: 15px;
      }
   }

   &__bottom {
      & p {
         font-size: 14px;
         margin-bottom: 10px;
      }

      & a {
         &:hover span {
            color: #ba0f08;
         }

         display: inline-flex;
         align-items: center;

         & span {
            font-size: 22px;
            transition: all 0.3s ease 0s;
            font-weight: 600;
         }
      }
   }

   &__phone-icon {
      width: 22px;
      height: 22px;

      & svg {
         fill: #ba0f08;
      }
   }
}

@media (max-width: 768px) {
   .mobile-menu {
      display: block;
   }
}

@media (max-width: 440px) {
   .mobile-menu__content {
      padding: 69px 20px 49px 20px;
   }
}
