.benefits {
   padding: 53px 0 35px;

   &__items {
      display: flex;
      justify-content: space-between;
   }

   &__item {
      display: flex;
      align-items: center;
      margin-right: 10px;
      width: 100%;
      max-width: 211px;

      &:last-child {
         margin-right: 0;
         justify-content: flex-end;
      }
   }

   &__icon {
      margin-right: 10px;
      width: 40px;
      height: 40px;

      & svg {
         stroke: #ba0f08;
      }
   }

   &__text {
      max-width: 160px;

      & span {
         font-size: 15px;
         line-height: 17 /15 * 100%;
      }
   }
}

@media (max-width: 1140px) {
   .benefits__items {
      flex-wrap: wrap;
      justify-content: space-between;
   }

   .benefits__item {
      flex: 0 0 31.33%;
      margin-bottom: 15px;
      max-width: 100%;
      margin-right: 0;
   }

   .benefits__item:last-child {
      justify-content: flex-start;
   }
}

@media (max-width: 991px) {
   .benefits {
      padding: 40px 0 20px;
   }
}

@media (max-width: 768px) {
   .benefits__item {
      flex: 0 0 48%;
   }

   .benefits__text {
      max-width: 100%;
   }
}

@media (max-width: 550px) {
   .benefits__item {
      flex: 0 0 100%;
   }

   .benefits__icon {
      margin-right: 10px;
      width: 33px;
      height: 34px;
   }

   .benefits {
      padding: 24px 0 20px;
   }
}
