.product {
   padding: 26px 0 5px;
}

.consult {
   padding: 60px 0 87px;
   background: #a7daea;

   &__title {
      text-align: center;
      margin-bottom: 8px;
   }

   &__form-body {
      display: flex;
      justify-content: space-between;
      margin: -10px;
   }

   &__subtitle {
      text-align: center;
      margin-bottom: 35px;
   }

   &__input {
      max-width: 305px;
      flex: 0 0 calc(25% - 30px);
      width: 100%;
      margin: 10px;

      & button {
         line-height: 55px;
      }
   }
}

@media (max-width: 991px) {
   .consult__form-body {
      flex-wrap: wrap;
   }

   .consult {
      padding: 30px 0 45px;
   }

   .consult__input {
      max-width: 100%;
      flex: 0 0 calc(50% - 20px);
   }
}

@media (max-width: 700px) {
   .consult__input {
      flex: 0 0 calc(100% - 20px);
   }

   .product {
      padding: 15px 0 5px;
   }

   .consult__title {
      margin-bottom: 3px;
   }

   .consult__subtitle {
      margin-bottom: 15px;
   }
}

@media (max-width: 550px) {
   .consult__input button {
      line-height: 40px;
   }

   .consult {
      padding: 25px 0 25px;
   }
}
