.details {
   padding: 55px 0 27px;

   &__content {
      display: flex;
   }

   &__column {
      flex: 0 0 50%;
   }

   &__block {
      padding-right: 15px;
      margin-bottom: 30px;
   }

   &__title {
      margin-bottom: 18px;
   }

   &__list {
      & li {
         font-size: 18px;
         line-height: 40 /18 * 100%;
         color: #5a5a5a;

         & span {
            font-weight: 600;
            margin-left: 7px;
         }
      }
   }

   &__warning {
      margin-top: 14px;
   }

   &__warning-title {
      margin-bottom: 15px;
   }

   &__warning-info {
      & p {
         white-space: pre-line;
         font-size: 18px;
         line-height: 25 /18 * 100%;
      }
   }
}

@media (max-width: 991px) {
   .details__content {
      flex-wrap: wrap;
   }

   .details__warning {
      margin-top: 5px;
   }

   .details__block {
      padding-right: 0;
      margin-bottom: 15px;
   }

   .details__column {
      flex: 0 0 100%;
   }

   .details__title {
      margin-bottom: 4px;
   }
}

@media (max-width: 768px) {
   .details {
      padding: 30px 0 27px;
   }
}

@media (max-width: 550px) {
   .details__list li {
      font-size: 14px;
   }

   .details__warning-info p {
      font-size: 14px;
   }
}
