.sport {
   padding: 15px 0 50px;

   &__title {
      text-align: center;
      margin-bottom: 15px;
   }

   &__subtitle {
      max-width: 888px;
      margin: 0 auto 25px;
      text-align: center;
   }

   &__body {
      display: flex;
   }

   & .card__slider {
      max-height: 200px;
      margin-bottom: 5px;
      padding-bottom: 5px;
   }

   &__items {
      display: flex;
      flex-wrap: wrap;
   }

   &__catagory {
      flex: 0 0 300px;
   }

   &__main {
      max-width: calc(100% - 300px);

      flex: 1 1 auto;
   }

   & .filter__item {
      flex: calc(33.33% - 20px);
      max-width: calc(33.33% - 20px);
   }

   & .filter__head {
      padding-bottom: 0;
      border-bottom: none;
      margin-bottom: 20px;
   }
}

.sport-category {
   margin-right: 15px;
   padding-right: 15px;

   &__title {
      font-size: 24px;
      line-height: 24 /24 * 100%;
      margin-bottom: 20px;
   }

   &__list {
      & li {
         margin-bottom: 10px;

         &:last-child {
            margin-bottom: 0;
         }

         &:hover span {
            color: #ba0f08;
         }

         &:hover svg {
            fill: #ba0f08;
         }

         & a {
            &._active svg {
               transform: rotate(180deg);
            }

            padding-right: 10px;
            width: 100%;
            display: inline-flex;
            align-items: center;

            & span {
               transition: all 0.3s ease 0s;
               font-size: 18px;
               line-height: 24 /18 * 100%;
            }
         }
      }
   }

   &__icon-first {
      width: 12px;
      height: 18px;
      margin-left: auto;
      transform: rotate(90deg);

      & svg {
         transition: all 0.3s ease 0s;
         fill: #303030;
      }
   }

   &__sublist {
      padding: 10px 0 10px 10px;
      max-width: 229px;

      & li {
         & a {
            &._active svg {
               transform: rotate(180deg);
            }

            padding-right: 20px;
            display: inline-flex;
            align-items: center;
            font-size: 17px;
            line-height: 24 /17 * 100%;

            & span {
               font-size: 17px;
               line-height: 24 /17 * 100%;
            }
         }
      }
   }

   &__icon-two {
      margin-left: auto;
      width: 11px;
      transform: rotate(90deg);
      height: 15px;

      & svg {
         transition: all 0.3s ease 0s;
      }
   }

   &__third-list {
      padding: 10px 0 10px 10px;

      & li {
         &:hover a {
            color: #ba0f08;
         }

         & a {
            transition: all 0.3s ease 0s;
            font-size: 15px;
            line-height: 24 /17 * 100%;
         }
      }
   }
}

@media (max-width: 1300px) {
   .sport__catagory {
      flex: 0 0 20%;
   }

   .sport__main {
      max-width: 79%;
   }

   .sport .buttons-buy__one-click {
      padding: 0px 70px 0 15px;
   }

   .sport .buttons-buy__basket-btn {
      max-width: 54px;
   }
}

@media (max-width: 991px) {
   .sport-category__list li a span {
      font-size: 16px;
   }

   .sport-category__sublist li a,
   .sport-category__sublist li a span {
      font-size: 15px;
   }

   .sport .filter__item {
      flex: calc(50% - 20px);
      max-width: calc(50% - 20px);
   }

   .sport__catagory {
      flex: 0 0 27%;
   }

   .sport__main {
      max-width: 71%;
   }

   .sport .filter-head__select {
      font-size: 16px;
      margin-right: 18px;
   }

   .select__current {
      font-size: 16px;
   }
}

@media (max-width: 768px) {
   .sport__catagory {
      display: none;
   }

   .breadcrumb__list {
      flex-wrap: wrap;

      & li {
         margin-bottom: 10px;
      }
   }

   .sport__main {
      max-width: 100%;
   }
}

@media (max-width: 550px) {
   .sport .filter__item {
      flex: calc(100% - 20px);
      max-width: calc(100% - 20px);
   }
}
