.sale {
   padding: 100px 0 75px;

   &__left,
   &__right {
      &.slick-disabled {
         opacity: 0;
         visibility: hidden;
      }

      position: absolute;
      top: 50%;
      transform: translateY(-50%);

      & .icon {
         height: 50px;
         width: 30px;
      }

      & svg {
         fill: #979797;
      }
   }

   &__left {
      left: -60px;
   }

   &__right {
      right: -60px;
   }
}

.sale-top {
   margin-bottom: 39px;
   background: #ffffff;
   box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
   border-radius: 15px;
   padding: 32px 0;
   text-align: center;

   &__label {
      font-weight: bold;
      font-size: 30px;
      line-height: 40 /30 * 100%;
      margin-bottom: 15px;
      color: #ba0f08;
   }

   &__title {
      position: relative;
      width: max-content;
      margin: 0 auto 20px;

      &::after,
      &::before {
         position: absolute;
         content: "";
         background-repeat: no-repeat;
         background-size: contain;
         top: 100%;
         width: 45px;
         height: 45px;
      }

      &::after {
         background-image: url("../img/line/arrow.png");
         left: -64px;
      }

      &::before {
         background-image: url("../img/line/arrow2.png");
         right: -64px;
      }

      font-weight: bold;
      font-size: 36px;
      line-height: 40 /36 * 100%;
      color: #4b4b4b;
   }

   &__descr {
      font-size: 22px;
      color: #4b4b4b;
      line-height: 25 /22 * 100%;
   }
}

.sale-nav__left {
   left: -15px;
}

.sale-nav__right {
   right: -15px;
}

.sale-nav__left,
.sale-nav__right {
   &.slick-disabled {
      opacity: 0;
      visibility: hidden;
   }

   position: absolute;
   top: 50%;
   transform: translateY(-50%);

   & .icon {
      height: 50px;
      width: 30px;
   }

   & svg {
      fill: #979797;
   }
}

.sale-main {
   background: #ffffff;
   box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
   border-radius: 15px;
   display: flex;

   &__sliders {
      padding: 49px 50px 64px 68px;
      max-width: 684px;
   }

   &__title {
      font-weight: 600;
      font-size: 45px;
      margin-bottom: 45px;
   }

   &__slider {
      max-width: 440px;
      min-height: 150px;
      margin: 0 auto 53px;
      max-height: 300px;
   }

   &__slide-img {
      max-width: 440px;
      width: 100%;
      min-height: 100px;
      padding-bottom: 68%;
      margin: 0 auto;
   }

   &__slide-nav {
      margin: 0 23px;
      padding: 8px;

      &.slick-current {
         border: 1px solid #979797;
         border-radius: 5px;
      }
   }

   &__img-nav {
      min-height: 40px;
      padding-bottom: 65%;
   }

   &__content {
      background: #eaf2f8;
      padding: 59px 63px;
      flex: 1 1 auto;
   }

   &__item {
      padding: 32px 5px 16px;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      border-bottom: 1px solid #c7c7c7;
   }

   &__radio-box {
      padding-left: 48px;
   }

   &__link {
      padding-left: 48px;

      & a {
         font-size: 18px;
         color: #4b4b4b;
         text-decoration: underline;
         transition: all 0.3s ease 0s;

         &:hover {
            color: #ba0f08;
         }
      }
   }

   &__countity {
      display: flex;
   }

   &__bottom {
      margin-top: 58px;
      display: flex;
      align-items: flex-end;
   }

   &__old-price {
      margin-bottom: 6px;
      font-size: 18px;
      text-decoration: line-through;
   }

   &__new-price {
      font-weight: 600;
      font-size: 36px;
   }

   & .buttons-buy__one-click {
      height: 55px;
   }

   &__buy {
      margin-left: 15px;
      max-width: 273px;
      width: 100%;
   }
}

@media (max-width: 1350px) {
   .buttons-buy {
      margin-left: auto;
      max-width: 230px;
   }
}

@media (max-width: 1250px) {
   .sale-main__content {
      padding: 46px 22px;
   }

   .sale-main__radio-box {
      padding-left: 5px;
   }

   .sale-main__item {
      padding: 16px 5px 12px;
   }

   .sale-main__new-price {
      font-size: 30px;
      white-space: nowrap;
   }

   .quick.fixed .buttons-buy {
      margin-left: 0;
   }

   .radiobox__container {
      padding-left: 43px;
      font-size: 25px;
   }

   .radiobox__radiomark {
      width: 27px;
      height: 27px;
   }

   .sale-main__slide-img {
      padding-bottom: 54%;
      max-width: 356px;
   }

   .sale-main__bottom {
      flex-direction: column;
      align-items: flex-start;
      margin-top: 20px;
   }

   .sale-main__buy {
      margin-left: 0;
      margin-top: 20px;
      width: auto;
   }
}

@media (max-width: 1090px) {
   .sale-main {
      flex-direction: column;
   }

   .sale-main__title {
      text-align: center;
   }

   .sale-main__sliders {
      padding: 37px 30px 35px 30px;
      max-width: 100%;
      margin: 0 auto 30px;
   }

   .sale-main__slider-nav {
      max-width: 635px;
      margin: 30px auto 0;
   }
}

@media (max-width: 768px) {
   .sale-main__title {
      font-size: 32px;
      margin-bottom: 31px;
   }

   .sale__left {
      left: -31px;
   }

   .sale__right {
      right: -31px;
   }

   .sale-main__content {
      padding: 25px 15px;
   }

   .sale {
      padding: 53px 0 43px;
   }
}

@media (max-width: 550px) {
   .sale-main__slide-img img {
      object-fit: contain;
   }

   .radiobox__container {
      padding-left: 36px;
      font-size: 20px;
   }

   .sale-main__slider {
      margin: 0 auto 29px;
   }

   .sale-main__link a {
      font-size: 15px;
   }

   .sale-nav__right {
      right: -24px;
   }

   .sale-nav__left {
      left: -24px;
   }

   .sale {
      padding: 30px 0 30px;
   }

   .sale-main__link {
      padding-left: 37px;
   }

   .radiobox__container .radiobox__radiomark::after {
      width: 10px;
      height: 10px;
   }

   .radiobox__radiomark {
      width: 20px;
      height: 20px;
   }

   .input-btn,
   .minus-btn,
   .plus-btn {
      font-size: 22px;
   }

   .input-btn {
      margin: 0 2px;
      max-width: max-content;
   }

   .sale-main__title {
      font-size: 20px;
   }

   .sale-main__sliders {
      padding: 20px 30px;
      max-width: 100%;
      margin: 0 auto 10px;
   }

   .sale-main .buttons-buy__one-click {
      height: 40px;
   }

   .sale-main__slide-nav {
      margin: 0 8px;
      padding: 5px;
   }

   .sale-main__content {
      padding: 15px 6px;
   }

   .input-btn,
   .minus-btn,
   .plus-btn {
      flex: 0 0 33%;
      padding: 0 9px;
   }
}
