.goods {
   padding: 28px 0 50px;

   &--bottom {
      padding: 28px 0 80px;
   }

   &__title {
      margin-bottom: 15px;
      text-align: center;
   }

   &__subtitle {
      text-align: center;
      max-width: 686px;
      margin: 0 auto 32px;
   }

   &__more-btn {
      margin: 20px auto 0;
      max-width: 216px;
      width: 100%;
   }
}

.goods-slider__wrapper {
   position: relative;
}

.goods-slider {
   &--height {
      & .card {
         max-width: 100%;

         & .card__img {
            padding: 0;
            height: 150px;
         }
      }
   }

   &__left,
   &__right {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
   }

   &__right {
      right: -20px;
   }

   &__left {
      left: -20px;
   }
}

.slick-list,
.slick-track {
   display: flex;
}

.goods-slide {
   margin: 0 10px;
   padding: 20px 0;
}

.goods {
   & .card {
      &__slider {
         max-height: 200px;
         margin: 10px auto;
         padding-bottom: 7px;
      }
   }
}

@media (max-width: 991px) {
   .goods {
      padding: 20px 0 20px;
   }
}
