.form-review {
   background-color: #a7daea;
   padding: 60px 0 68px;
   background-image: url("../img/home/bg2.png");
   background-repeat: no-repeat;
   background-size: auto 100%;
   background-position: center;

   &__title {
      max-width: 750px;
      margin: 0 auto 30px;
      text-align: center;
   }
}

.review-form {
   &__body {
      max-width: 846px;
      margin: 0 auto;
   }

   &__inputs {
      margin: -7.5px;
      display: flex;
      flex-wrap: wrap;
   }

   &__input {
      flex: 0 0 calc(50% - 15px);
      margin: 7.5px;
   }

   &__textarea {
      margin-top: 15px;

      & textarea {
         height: 120px;
         resize: none;
         width: 100%;
         font-size: 18px;
         line-height: 23 /18 * 100%;
         color: #4b4b4b;
         padding: 17px 25px;
         font-weight: 600;
         background: #ffffff;
         border-radius: 10px;
      }
   }

   &__buttons {
      margin-top: 25px;
      display: flex;
      align-items: center;
   }

   &__add-btn {
      position: relative;
      padding-left: 30px;

      &::after {
         content: "";
         position: absolute;
         top: 50%;
         left: 0;
         background-repeat: no-repeat;
         background-image: url("../img/sprite/screpka.svg");
         width: 22px;
         height: 22px;
         transform: translateY(-50%);
      }

      & label {
         cursor: pointer;
         display: inline-flex;
         align-items: center;
         font-size: 18px;
         color: #4b4b4b;
      }

      & input {
         opacity: 0;
         position: absolute;
         z-index: -1;
      }

      display: inline-flex;
      align-items: center;
   }

   &__icon {
      margin-right: 9px;
      width: 22px;
      height: 22px;
   }

   &__send {
      max-width: 196px;
      margin-left: auto;
   }
}

@media (max-width: 768px) {
   .form-review {
      padding: 40px 0 40px;
   }

   .review-form__input {
      flex: 0 0 calc(100% - 15px);
      margin: 7.5px;
   }
}

@media (max-width: 550px) {
   .review-form__textarea textarea {
      font-size: 14px;

      color: #4b4b4b;
      padding: 10px 14px;
   }

   .form-input input {
      font-size: 14px;

      padding: 10px 10px 14px 14px;
   }

   .review-form__add-btn label {
      font-size: 15px;
   }
}
