.discount {
   padding: 29px 0 20px;

   &--bottom {
      padding: 29px 0 100px;
   }

   &__head {
      text-align: center;
      margin-bottom: 0px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      min-height: 44px;
   }

   &__title {
      margin: 0 auto;
   }

   &__all-link {
      position: absolute;
      right: 0;

      & svg {
         transition: all 0.3s ease 0s;
      }

      &:hover svg {
         fill: #ba0f08;
      }

      &:hover span {
         color: #ba0f08;
      }

      display: inline-flex;
      align-items: flex-end;

      & span {
         transition: all 0.3s ease 0s;
         font-size: 18px;
         line-height: 18 /18 * 100%;
      }
   }

   &__all-icon {
      width: 15px;
      height: 15px;
      margin-left: 7px;

      & svg {
         fill: #303030;
      }
   }

   &__quick-wrapper {
      position: relative;
   }
}

.discount-slider {
   margin: 0 -10px;
   display: flex;
   z-index: 9;

   & .slick-track {
      display: flex;
   }

   &__slide {
      margin: 0 10px;
      padding: 36px 0 15px;
      height: auto;
   }

   &__left,
   &__right {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
   }

   &__left {
      left: -24px;
   }

   &__right {
      right: -24px;
   }
}

.card__popup {
   position: absolute;
   top: 100%;
   width: 100%;
   background-color: red;
   height: 200px;
}

@media (max-width: 1360px) {
   .discount-slider__right {
      right: -5px;
   }

   .discount-slider__left {
      left: -5px;
   }

   .discount-slider__left,
   .discount-slider__right {
      transform: translateY(0);
      top: 36%;
   }
}

@media (max-width: 1110px) {
   .card__body {
      text-align: center;
   }

   .card {
      padding: 17px 16px 20px;
   }

   .card__buttons {
      margin: 0 auto 10px;
   }
}

@media (max-width: 991px) {
   .discount {
      padding: 18px 0 14px;
   }
}

@media (max-width: 768px) {
   .buttons-buy__one-click {
      padding: 0 70px 0 18px;
   }
}

@media (max-width: 550px) {
   .card__img {
      max-width: 350px;

      padding: 0px 0 37%;
      margin-bottom: 15px;
   }

   .discount__title {
      margin: 0 7px 0 0;
   }

   .discount__all-link span {
      font-size: 15px;
   }

   .discount__head {
      justify-content: flex-start;
   }

   .card__price {
      margin: 20px 0 15px;
   }

   .discount__all-link {
      left: 0;
      top: 47px;
   }

   .card__body {
      min-height: auto;
   }

   .discount-slider__left,
   .discount-slider__right {
      top: 53%;
   }
}
