body {
   margin: 0;
   padding: 0;
   // word-break: break-word;
   overflow-x: hidden;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   font-size: 14px;
   font-family: "Segou";
   color: #303030;

   &._lock {
      overflow: hidden;
   }

   &.lock {
      overflow: hidden;
   }
}

@media (max-width: 767px) {
   body.hidden {
      overflow: hidden;
   }
}

html {
   box-sizing: border-box;
}

*,
*::after,
*::before {
   box-sizing: inherit;
}

textarea {
   border: none;
   overflow: auto;
   outline: none;
   -webkit-box-shadow: none;
   -moz-box-shadow: none;
   box-shadow: none;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
   transition: background-color 5000s ease-in-out 0s;
   -webkit-text-fill-color: #4b4b4b !important;
}

* {
   -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
   display: inline-block;

   color: inherit;
   text-decoration: none;

   cursor: pointer;
}

button {
   background-color: transparent;
   border: none;
   outline: none;
   cursor: pointer;
}

ul,
li {
   margin: 0;
   padding: 0;

   list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
   margin: 0;
}

img {
   height: auto;
   max-width: 100%;
}

input {
   width: 100%;

   outline: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
   margin: 0;

   appearance: none;
}

input[type="number"] {
   appearance: textfield;
}

.visually-hidden {
   position: absolute;

   width: 1px;
   height: 1px;
   margin: -1px;
   padding: 0;
   overflow: hidden;

   white-space: nowrap;

   border: 0;

   clip: rect(0 0 0 0);
   clip-path: inset(100%);
}

.wrapper {
   display: flex;
   flex-direction: column;
   min-height: 100%;
   width: 100%;
   overflow: hidden;
}

.page {
   flex: 1 1 auto;
   margin-top: 218px;
}

.container {
   width: 100%;
   max-width: 1310px;
   padding: 0 15px;
   margin: 0 auto;
}

.icon {
   display: inline-flex;
   align-items: center;
   width: 100%;
   justify-content: center;
}

.icon svg {
   height: 100%;
   width: 100%;
   transition: all 0.3s ease 0s;
}

.ibg {
   position: relative;

   img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      object-fit: cover;
   }
}

._tabs-block {
   visibility: hidden;
   height: 0;

   &._active {
      visibility: visible;
      height: auto;
   }
}
