.top {
   padding: 51px 0 78px;
   position: relative;

   &__head {
      text-align: center;
      margin-bottom: 26px;
   }

   &__title {
      margin-bottom: 10px;
   }

   &__show-all {
      font-size: 20px;
      line-height: 18px;

      & a {
         text-decoration: underline;
         transition: all 0.3s ease 0s;
         color: #4b4b4b;

         &:hover {
            color: #ba0f08;
         }
      }

      position: absolute;
      top: 360px;
      right: 0;
   }
}

.top-slider {
   &__head {
      margin-bottom: 15px;
   }

   &__head-left {
      left: -25px;
   }

   &__head-right {
      right: -25px;
   }

   &__head-left,
   &__head-right {
      position: absolute;
      cursor: pointer;
      top: 50%;
      transform: translateY(-50%);
   }

   &__head-slide {
      margin: 0 5px;
   }

   &__head-item {
      padding: 0 20px;
      width: 100%;
      text-align: center;
      max-width: 247px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      height: 64px;
      background: #ffffff;
      border: 1px solid #4b4b4b;
      border-radius: 30px;
      font-size: 18px;
      line-height: 20 /18 * 100%;
      transition: all 0.3s ease 0s;

      &.active {
         background: #98c5e2;
         border: 1px solid #4b4b4b;
      }
   }

   &__card-wrapper {
      position: relative;
   }

   &__card-left {
      left: -20px;
   }

   &__card-right {
      right: -20px;
   }

   &__card-right,
   &__card-left {
      position: absolute;
      top: 142px;
   }

   &__slide {
      margin: 0 9px;
      padding: 35px 0 20px;
   }
}

.top-slider__card {
   & .card {
      height: auto;
      position: relative;
      text-align: center;
      transition: all 0.3s ease 0s;

      &.active .card__bottom {
         opacity: 1;
         visibility: visible;
         max-height: 100%;
      }

      &:hover .card__bottom {
         opacity: 1;
         visibility: visible;
         max-height: 100%;
      }

      &__descr {
         font-weight: 600;
         font-size: 19px;
      }

      &__img {
         max-width: 271px;
         padding-bottom: 49%;
      }

      &__bottom {
         transition: all 0.3s ease 0s;
         text-align: center;
         opacity: 0;
         visibility: hidden;
         max-height: 0;
      }

      &__buttons {
         margin: 0 auto 15px;
      }
   }
}

@media (max-width: 1340px) {
   .top-slider__head-left {
      left: -10px;
   }

   .top-slider__head-right {
      right: -10px;
   }

   .top-slider__head {
      padding: 0 20px;
   }
}

@media (max-width: 991px) {
   .top {
      padding: 21px 0 80px;
   }

   .top__show-all {
      top: 324px;
   }
}

@media (max-width: 768px) {
   .top-slider__head-item {
      max-width: 300px;
   }
}

@media (max-width: 550px) {
   .top-slider__head-item {
      max-width: 100%;
   }

   .top__show-all {
      top: 384px;
   }
}

@media (max-width: 440px) {
   .top__show-all {
      top: 352px;
   }
}

@media (max-width: 380px) {
   .top__show-all {
      top: 326px;
   }
}
