.gift {
   padding: 45px 0 39px;
}

.gift-slider .slick-list {
   margin: 0 -15px;
}

.gift-slide {
   padding: 15px 0;
   position: relative;
   margin: 0 20px;

   &__wrap {
      background: #ffffff;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
      border-radius: 15px;
      padding: 66px 46px 111px 73px;
   }

   &__head {
      align-items: center;
      display: flex;
      justify-content: space-between;
      margin-bottom: 37px;
   }

   &__title {
      font-weight: 600;
      font-size: 45px;
      line-height: 45 /45 * 100%;
      color: #ba0f08;
   }

   &__option {
      align-items: center;
      display: flex;

      & ul {
         margin-left: 26px;
         display: flex;

         & li {
            margin-right: 25px;

            &:hover button {
               color: #ba0f08;
            }

            &:last-child {
               margin-right: 0;
            }

            & button {
               transition: all 0.3s ease 0s;
               min-width: 80px;
               border: 1px solid #c7c7c7;
               border-radius: 5px;
               line-height: 43px;
               display: inline-flex;
               align-items: center;
               justify-content: center;
               font-size: 22px;

               &.active {
                  color: #ffffff;
                  background-color: #ba0f08;
               }
            }
         }
      }
   }

   &__if {
      font-size: 24px;
      line-height: 30 /24 * 100%;
   }

   &__img-box {
      padding: 0 28px 0 63px;
      display: flex;
      justify-content: space-between;
      margin-bottom: 30px;
   }

   &__img {
      margin-right: 20px;
      max-width: 435px;
      width: 100%;
      padding: 0px 0px 30% 0px;
      min-height: 200px;
   }

   &__video {
      display: flex;
      justify-content: flex-end;
      width: 100%;
   }

   &__info {
      display: flex;
      justify-content: space-between;
   }

   &__sale-text {
      font-weight: 600;
      font-size: 22px;
      letter-spacing: 0.2em;
      color: #4b4b4b;
      margin-bottom: 7px;
   }

   &__descr {
      & p {
         font-weight: 600;
         font-size: 24px;
         line-height: 30px;

         & span {
            display: block;
            color: #ba0f08;
         }
      }
   }

   &__time {
      align-self: flex-end;

      & span {
         font-weight: 600;
         font-size: 16px;
      }
   }
}

.gift-slider__left {
   position: absolute;
   top: 50%;
   transform: translateY(-50%);
   left: -24px;
}

.gift-slider__right {
   position: absolute;
   top: 50%;
   transform: translateY(-50%);
   right: -24px;
}

.gift-slider__dots {
   position: absolute;
   bottom: 70px;
   left: 50%;
   transform: translateX(-50%);
}

.gift {
   &__warn {
      margin-top: 45px;
   }
}

.gift-warn {
   background-position: 13px -82px;
   background-color: #a7daea;
   background-image: url("../img/home/bg1.png");
   background-repeat: no-repeat;
   border-radius: 15px;
   overflow: hidden;
   padding: 53px 53px 24px 60px;

   &__body {
      display: flex;
      justify-content: space-between;
   }

   &__title {
      font-weight: bold;
      font-size: 40px;
      line-height: 40 /40 * 100%;
      color: #4b4b4b;
      margin-right: 20px;
   }

   &__head {
      display: flex;
      align-items: center;
      margin-bottom: 48px;
   }

   &__warning {
      font-weight: 600;
      font-size: 35px;
      line-height: 100%;
      color: #ba0f08;
      margin-right: 28px;
   }

   &__text {
      z-index: 2;
      display: flex;
      align-items: center;

      & span {
         font-weight: 600;
         font-size: 24px;
      }

      & strong {
         font-weight: bold;
         font-size: 30px;
         color: #ba0f08;
         margin: 0 35px;
         position: relative;

         &::after {
            background-image: url("../img/home/giftbox.png");
            background-repeat: no-repeat;
            background-size: contain;
            position: absolute;
            content: "";
            top: 50%;
            left: 50%;
            height: 85px;
            width: 68px;
            z-index: -1;
            transform: translate(-50%, -58px);
         }
      }
   }

   &__form-body {
      display: flex;
      justify-content: flex-end;
   }

   &__input {
      max-width: 266px;
      margin-right: 24px;
      width: 100%;
   }

   &__btn {
      line-height: 55px;
      white-space: nowrap;
   }
}

@media (max-width: 1350px) {
   .gift-slider__right {
      right: -6px;
   }

   .gift-slider__left {
      left: -6px;
   }
}

@media (max-width: 1300px) {
   .gift-slide__wrap {
      padding: 50px 35px 96px 50px;
   }

   .gift-slide__title {
      font-size: 39px;
      margin-right: 10px;
   }

   .gift-slide__img {
      margin-right: 10px;
      max-width: 42%;
      width: 100%;
      padding: 0 0 30%;
      min-height: 200px;
   }

   .yt-lazyload {
      max-width: 78%;
   }

   .gift-slide__option ul {
      flex-wrap: wrap;
   }

   .gift-slide__img-box {
      padding: 0 32px 0 32px;
   }

   .gift-slide {
      padding: 15px 0;
   }
}

@media (max-width: 1190px) {
   .gift-slide__option ul li {
      margin-right: 11px;
   }

   .gift-slide__if {
      font-size: 20px;
   }

   .gift-slide__title {
      font-size: 34px;
   }
}

@media (max-width: 1050px) {
   .gift-slide__head {
      flex-direction: column;
   }

   .gift-warn__text span {
      font-weight: 600;
      font-size: 21px;
   }

   .gift-warn__btn {
      padding: 0 13px;
   }

   .gift-slide__title {
      margin-bottom: 20px;
   }

   .gift-slide__img-box {
      padding: 0;
   }
}

@media (max-width: 991px) {
   .gift-slide__info {
      flex-direction: column;
   }

   .gift-warn__btn,
   .gift-warn__input {
      margin-top: 10px;
   }

   .gift-warn__head {
      flex-wrap: wrap;
   }

   .gift-slide__time {
      align-self: flex-start;
      margin-top: 15px;
   }

   .gift-warn__body {
      flex-wrap: wrap;
   }

   .gift-warn__title {
      font-size: 31px;
      margin-bottom: 15px;
      margin-right: 15px;
   }

   .gift__warn {
      margin-top: 22px;
      padding: 50px 36px 17px 41px;
   }

   .gift-warn__form-body {
      flex-wrap: wrap;
      justify-content: flex-start;
   }

   .gift-warn__head {
      margin-bottom: 24px;
   }

   .gift-warn__warning {
      font-size: 29px;

      margin-right: 21px;
   }

   .gift-warn__text span {
      font-size: 18px;
   }
}

@media (max-width: 768px) {
   .gift-slide__descr p {
      font-weight: 600;
      font-size: 18px;
      line-height: 26px;
   }

   .gift {
      padding: 29px 0 25px;
   }

   .gift-slide__wrap {
      padding: 35px 24px 50px 35px;
   }

   .gift-slider__dots {
      bottom: 37px;
   }

   .gift-slide__img-box {
      flex-direction: column;
   }

   .gift-slide__img {
      max-width: 77%;
      width: 100%;
      padding: 0px 0 43%;
      margin: 0 auto 20px;

      & img {
         object-fit: contain;
      }
   }

   .yt-lazyload {
      min-height: auto;
      max-width: 95%;
      margin: 0 auto;
      padding-bottom: 54%;
   }

   .gift-warn__btn {
      margin-top: 10px;
   }

   .gift-warn__input {
      margin-top: 10px;
   }
}

@media (max-width: 550px) {
   .gift-slide__title {
      font-size: 26px;
      margin-bottom: 0;
      margin-right: 0;
      text-align: left;
   }

   .gift-slide {
      margin: 5px;
   }

   .gift .slick-list {
      margin: 0;
   }

   .gift-warn__btn-wrap {
      margin-top: 15px;
      max-width: 266px;
      width: 100%;
   }

   .gift-slide__option ul li button {
      min-width: 71px;
      line-height: 31px;
      font-size: 16px;
   }

   .gift-slide__descr p {
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
   }

   .slider-btn {
      max-width: 37px;
      height: 37px;
   }

   .slider-btn__icon {
      width: 15px;
      height: 14px;
   }

   .gift-slide__option {
      flex-wrap: wrap;
      flex: 0 0 100%;
      margin-top: 15px;
   }

   .gift-slide__option ul {
      margin-left: 0;
   }

   .gift-slide__wrap {
      padding: 23px 14px 49px 14px;
   }

   .gift-warn__title {
      font-size: 26px;
   }

   .gift-warn__text strong {
      font-size: 25px;

      margin: 0 20px;
   }

   .gift-warn__text strong::after {
      top: 143%;

      height: 55px;
      width: 41px;
   }

   .gift-warn__warning {
      font-size: 23px;
      margin-right: 11px;
   }

   .gift-warn__input {
      margin-right: 0;
   }

   .gift-warn__text span {
      font-size: 16px;
   }

   .gift-warn__head {
      margin-bottom: 12px;
   }

   .gift__warn {
      margin-top: 22px;
      padding: 21px 20px 20px 20px;
   }

   .gift-warn__btn {
      line-height: 40px;
   }
}
