.shop {
   padding: 38px 0 60px;

   & .input-btn,
   .minus-btn,
   .plus-btn {
      font-size: 24px;
   }

   .input-btn {
      margin: 0 4px;
      text-align: center;
      width: 30px;
   }

   &__title {
      text-align: center;
      margin-bottom: 45px;
   }

   &__items {
      background: #ffffff;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
      border-radius: 15px;
      padding: 40px;
   }

   &__item {
      display: flex;
      padding: 20px 50px 20px 0;
      border-bottom: 1px solid #c7c7c7;
      align-items: center;
      justify-content: space-between;

      &:last-child {
         border-bottom: none;
      }
   }

   &__product {
      flex: 1 1 auto;
      display: flex;
      align-items: center;
      padding-right: 10px;
   }

   &__img {
      min-height: 70px;
      max-width: 150px;
      padding-bottom: 12%;
      width: 100%;
      margin-right: 15px;

      & img {
         object-fit: contain;
      }
   }

   &__name {
      font-size: 24px;
      max-width: 327px;
   }

   &__prices {
      display: flex;
      align-items: center;
      white-space: nowrap;
   }

   &__price-one {
      width: 100%;
   }

   &__price {
      font-size: 24px;
      display: flex;
      align-items: center;
      width: 100%;
   }

   &__price-all {
      margin-left: 65px;
      width: 100%;
   }

   &__delete {
      display: inline-flex;
      align-items: center;
      cursor: pointer;
      margin-left: 75px;
   }

   &__delete-icon {
      width: 20px;
      height: 23px;

      & svg {
         fill: #4b4b4b;
      }
   }

   & .quantity {
      margin-right: 80px;
   }
}

.shop-bottom {
   margin-top: 15px;
   display: flex;
   align-items: center;

   &__left {
      display: flex;
      align-items: center;
   }

   &__icon {
      width: 64px;
      height: 64px;
      margin-right: 20px;

      & svg {
         fill: #ba0f08;
      }
   }

   &__info {
      font-size: 24px;
      line-height: 27 /24 * 100%;
   }

   &__link {
      display: block;

      & a {
         font-size: 18px;
      }
   }

   &__right {
      display: flex;
      align-items: center;
      margin-left: auto;

      & span {
         font-size: 24px;
         line-height: 32 /24 * 100%;
      }
   }

   &__price {
      font-weight: 600;
      font-size: 24px;
      line-height: 32 /24 * 100%;
      margin-left: 35px;
   }
}

.gift-slide__head--center {
   justify-content: center;
   margin-bottom: 17px;
   margin-top: 50px;

   & .gift-slide__title {
      margin-right: 20px;
   }
}

@media (max-width: 1180px) {
   .shop__price-all {
      margin-left: 28px;
   }

   .shop__prices {
      white-space: nowrap;
   }

   .shop__delete {
      margin-left: 20px;
   }

   .shop .quantity {
      margin-right: 50px;
   }
}

@media (max-width: 1050px) {
   .shop__item {
      padding: 14px 8px 14px;
   }

   .shop__items {
      padding: 20px;
   }
}

@media (max-width: 991px) {
   .shop__name {
      font-size: 20px;
      max-width: 327px;
   }

   .shop .input-btn,
   .shop .minus-btn,
   .shop .plus-btn {
      font-size: 20px;
      line-height: 110%;
   }

   .shop__price {
      font-size: 20px;
   }

   .shop__item {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
   }

   .shop__product {
      width: 100%;
      margin-bottom: 30px;
   }
}

@media (max-width: 768px) {
   .shop__delete {
      position: absolute;
      top: 10px;
      right: 10px;
   }

   .shop__item {
      position: relative;
      text-align: center;
      align-items: center;
   }

   .shop__price-all {
      margin-left: 0;
   }

   .shop__name {
      max-width: 100%;
   }

   .shop__product {
      flex-direction: column;
   }

   .shop__product {
      margin-bottom: 1px;
   }

   .shop__img {
      margin-right: 0;
      margin-bottom: 15px;
   }

   .shop__prices {
      flex-direction: column;
      align-items: center;
      margin-top: 10px;
   }

   .shop .quantity {
      margin-right: 0;
      margin-bottom: 10px;
   }

   .shop__price-one {
      margin-right: 15px;
   }
}

@media (max-width: 620px) {
   .shop-bottom__icon {
      width: 30px;
      height: 30px;
      margin-right: 10px;
   }

   .shop-bottom__info {
      font-size: 18px;
   }

   .shop-bottom__price,
   .shop-bottom__right span {
      font-size: 18px;
   }

   .shop {
      padding: 20px 0 34px;
   }

   .gift-slide__head--center {
      margin-bottom: 14px;
      margin-top: 8px;
   }
}

@media (max-width: 550px) {
   .shop .input-btn,
   .shop .minus-btn,
   .shop .plus-btn,
   .shop__name,
   .shop__price {
      font-size: 16px;
   }

   .shop-bottom {
      flex-direction: column;
      align-items: flex-start;
   }

   .shop-bottom__price {
      margin-left: 12px;
   }

   .shop-bottom__link a {
      font-size: 14px;
   }

   .shop-bottom__right {
      margin-left: 0;
      margin-top: 20px;
   }

   .shop-bottom__info,
   .shop-bottom__price,
   .shop-bottom__right span {
      font-size: 14px;
   }

   .shop__items {
      padding: 10px;
   }

   .shop__title {
      margin-bottom: 23px;
   }
}
