.quick {
   background: #ffffff;
   box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
   border-radius: 15px;
   position: relative;
   padding: 26px 0 68px 40px;
   position: absolute;
   top: calc(100% - 15px);
   left: 0;
   width: 100%;
   z-index: 10;
   display: block;
   visibility: hidden;
   opacity: 0;
   pointer-events: none;
   height: 0;

   &--relative {
      height: auto;
      visibility: visible;
      position: relative;
      box-shadow: none;
      opacity: 1;
      pointer-events: all;
   }

   &.active {
      height: auto;
      visibility: visible;
      opacity: 1;
      pointer-events: all;
   }

   &__title {
      font-size: 36px;
      line-height: 40 /36 * 100%;
      color: #979797;
      margin-bottom: 25px;
      font-weight: normal;
   }

   &__close {
      position: absolute;
      top: 35px;
      right: 35px;
      cursor: pointer;
      width: 22px;
      height: 22px;
   }

   &__close-icon {
      width: 22px;
      height: 22px;

      & svg {
         fill: #303030;
         stroke: #303030;
      }
   }

   &__main {
      display: flex;
      justify-content: space-between;
   }

   &__slider-container {
      width: 100%;
      max-width: 472px;
      margin-right: 30px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
   }

   &__block {
      max-width: 700px;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
   }

   &__details {
      border-bottom: 1px solid #c7c7c7;
      padding: 0px 20px 20px 0px;
   }

   &__name-product {
      font-size: 36px;
      line-height: 40px;
      line-height: 40 /36 * 100%;
      margin-bottom: 5px;
   }

   &__labels {
      display: flex;
      align-items: center;
   }

   &__procent {
      margin-right: 15px;
      background-color: #ba0f08;
   }

   &__circle {
      width: 42px;
      height: 42px;
      border-radius: 50%;
      display: inline-flex;
      align-items: center;
      justify-content: center;

      & span {
         font-weight: 600;
         font-size: 18px;
         color: #ffffff;
      }
   }

   &__hit {
      background-color: #2e8bc8;
   }

   &__features-head {
      display: flex;
      margin-bottom: 10px;
      align-items: center;
   }

   &__features-title {
      font-size: 24px;
      line-height: 40 /24 * 100%;
      margin-right: 100px;
   }

   &__features-link {
      font-size: 20px;

      color: #ba0f08;

      & a {
         transition: all 0.3s ease 0s;
         text-decoration: underline;

         &:hover {
            color: darkred;
         }
      }
   }

   &__list {
      & li {
         display: flex;
         align-items: center;
         margin-bottom: 15px;

         &:last-child {
            margin-bottom: 0;
         }

         & span {
            font-weight: 600;
            font-size: 18px;
            color: #5a5a5a;
         }
      }
   }

   &__ok-icon {
      width: 15px;
      height: 15px;
      margin-right: 10px;

      & svg {
         fill: #98c5e2;
      }
   }

   &__block-bottom {
      display: flex;
      align-items: center;
      margin-top: 25px;
      padding: 0 23px;
   }

   &__prices {
      display: flex;
      align-items: center;
   }

   &__price {
      font-weight: 600;
      font-size: 40px;
      margin-right: 20px;
   }

   &__old-price {
      font-size: 24px;
      text-decoration: line-through;
   }

   &__btn-buy {
      & button {
         height: 60px;
      }

      max-width: 280px;
      margin-left: auto;
   }

   &__complect {
      margin-top: 69px;
   }

   &__complect-title {
      font-size: 36px;
      line-height: 40 /36 * 100%;
      margin-bottom: 22px;
   }

   &__complect-items {
      display: flex;
      margin: 0 -10px;
   }

   &__complect-item {
      flex: 0 0 calc(25% - 10px);
      margin: 0 10px;
      background: #ffffff;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
      border-radius: 15px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      padding: 30px;
      transition: all 0.3s ease 0s;

      &:hover {
         box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.15);
      }
   }

   &__complect-img {
      min-height: 50px;
      padding-bottom: 58%;
      max-width: 200px;
      width: 100%;
      margin: 0 auto 20px;

      & img {
         object-fit: contain;
      }
   }

   &__complect-name {
      font-size: 18px;
      text-align: center;
   }

   &__complect-price {
      margin-top: 10px;
      font-weight: 600;
      font-size: 20px;
   }
}

.quick-slider {
   margin-bottom: 40px;

   &__right,
   &__left {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;

      &.slick-disabled {
         & svg {
            fill: #979797;
         }
      }
   }

   &__right {
      right: -25px;
   }

   &__left {
      left: -25px;
   }
}

.quick-slide {
   min-height: 200px;
   padding-bottom: 9%;

   & img {
      object-fit: contain;
   }

   &__nav {
      margin: 0 22px;
      height: 105px;
      width: 120px;
      transition: all 0.3s ease 0s;
      cursor: pointer;
      border: 1px solid transparent;

      &.slick-current {
         border: 1px solid #4b4b4b;
      }

      &:hover {
         border: 1px solid #4b4b4b;
      }

      & img {
         object-fit: contain;
      }
   }
}

@media (max-width: 1210px) {
   .quick__name-product {
      font-size: 28px;
   }

   .quick__block-bottom {
      flex-direction: column;
      align-items: flex-start;
   }

   .quick__btn-buy {
      margin-left: 0;
      margin-top: 20px;
   }
}

@media (max-width: 1050px) {
   .quick__slider-container {
      max-width: 41%;
   }

   .quick-slide__nav {
      height: 91px;
      width: 101px;
   }
}

@media (min-width: 991px) {
   .quick--small {
      & .quick__features-title {
         margin-right: 35px;
      }

      & .buttons-buy__one-click {
         padding: 0 70px 0 9px;
      }

      & .quick__features-link a {
         font-size: 16px;
      }

      & .quick__btn-buy {
         max-width: 200px;
      }

      & .quick__name-product {
         font-size: 32px;
      }

      & .quick__prices {
         flex-direction: column;
         align-items: flex-start;
      }

      & .quick__price {
         font-size: 35px;
         margin-right: 10px;
      }
   }
}

@media (max-width: 991px) {
   .quick {
      padding: 26px 0 34px 30px;
   }

   .quick__title {
      font-size: 30px;
      margin-bottom: 18px;
   }

   .quick__list li {
      margin-bottom: 10px;
   }

   .quick__name-product {
      font-size: 25px;
   }
}

@media (max-width: 768px) {
   .quick__main {
      flex-direction: column;
      justify-content: center;
      align-items: center;
   }

   .quick__slider-container {
      max-width: 80%;
      margin-right: 0;
      margin-bottom: 30px;
   }

   .quick-slide {
      padding-bottom: 10%;
   }

   .quick__block-bottom {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
   }

   .quick__btn-buy {
      margin-left: 15px;
      margin-top: 0;
      max-width: 205px;

      & button {
         height: 40px;
      }
   }

   .quick__price {
      font-size: 31px;
   }

   .quick__complect {
      margin-top: 20px;
      padding-right: 30px;
   }

   .quick__complect-item {
      padding: 15px;
      flex: 0 0 calc(50% - 20px);
   }
}

@media (max-width: 620px) {
   .quick__btn-buy {
      margin-top: 15px;
      margin-left: 0;
   }

   .quick__features-link {
      font-size: 17px;
   }

   .quick-slide {
      min-height: auto;
   }
}

@media (max-width: 550px) {
   .quick__block-bottom {
      flex-direction: column;
      align-items: flex-start;
   }

   .quick__block-bottom {
      padding: 0;
      padding-right: 30px;
   }

   .quick__complect-items {
      flex-wrap: wrap;
   }

   .quick__complect-item {
      margin-bottom: 15px;
      flex: 0 0 calc(100% - 20px);

      &:last-child {
         margin-bottom: 0;
      }
   }

   .quick__name-product {
      font-size: 20px;
   }

   .quick__title {
      font-size: 25px;
      margin-bottom: 16px;
   }

   .quick__features-link {
      margin-bottom: 10px;
   }

   .quick__features-head {
      flex-wrap: wrap;
   }

   .quick__price {
      font-size: 24px;
   }

   .quick__btn-buy {
      max-width: 300px;
   }

   .quick__complect-title {
      font-size: 25px;
      margin-bottom: 17px;
   }

   .quick__complect-img {
      padding-bottom: 39%;
   }

   .quick__close {
      right: 18px;
      top: 20px;
   }

   .quick__complect-name {
      font-size: 15px;
   }
}

@media (max-width: 400px) {
   .quick__price {
      font-size: 21px;
   }

   .quick__old-price {
      font-size: 15px;
   }

   .quick {
      padding: 15px 0 20px 15px;
   }
}
