.section-info {
   padding: 35px 0 45px;

   &__title {
      margin-bottom: 11px;
      text-align: center;
   }

   &__text {
      max-width: 1124px;
      margin: 0 auto;
      text-align: center;

      & p {
         font-size: 18px;
         line-height: 26 /18 * 100%;
         text-align: center;
         color: #4b4b4b;
      }
   }
}

@media (max-width: 991px) {
   .section-info {
      padding: 20px 0 25px;
   }

   .title {
      font-size: 28px;
   }

   .section-info__text p {
      font-size: 16px;
   }
}

@media (max-width: 550px) {
   .title {
      font-size: 22px;
   }
}
