.popup {
   position: fixed;
   width: 100%;
   height: 100%;
   background-color: rgba(0, 0, 0, 0.8);
   top: 0;
   left: 0;
   opacity: 0;
   visibility: hidden;
   overflow-y: auto;
   overflow-x: hidden;
   transition: all 0.8s ease 0s;
   z-index: 10;
}

.popup.open {
   opacity: 1;
   visibility: visible;
}

.popup.open .popup__content {
   transform: perspective(600px) translate(0px, 0%) rotateX(0deg);
   opacity: 1;
}

.popup__area {
   position: absolute;
   width: 100%;
   height: 100%;
   top: 0;
   left: 0;
}

.popup__body {
   min-height: 100%;
   display: flex;
   align-items: center;
   justify-content: center;
   padding: 30px 10px;
   transition: all 0.8s ease 0s;
}

.popup__content {
   background-color: #fff;
   max-width: 1278px;
   width: 100%;
   position: relative;
   transition: all 0.8s ease 0s;
   opacity: 0;
   border-radius: 15px;
   box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
   border-radius: 15px;
   transform: scale(0.3) translate(0px, -100%);
}

.popup__close {
   position: absolute;
   right: 26px;
   top: 37px;
   width: 30px;
   height: 30px;
   cursor: pointer;

   &::after,
   &::before {
      content: "";
      width: 30px;
      transition: all 0.3s ease 0s;
      height: 1px;
      top: 50%;
      left: 50%;
      background-color: #979797;
      position: absolute;
   }

   &::after {
      transform: translate(-50%, -50%) rotate(45deg);
   }

   &::before {
      transform: translate(-50%, -50%) rotate(-45deg);
   }
}

.popup-day {
   &__title {
      margin-bottom: 7px;
   }

   &__content {
      padding: 47px 32px 0 68px;
   }

   &__list-city {
      margin-bottom: 20px;

      & ul {
         display: flex;
         flex-wrap: wrap;

         & li {
            margin-right: 4px;

            &:last-child {
               margin-right: 0;
            }

            & a {
               font-size: 18px;
               line-height: 20 /18 * 100%;
               transition: all 0.3s ease 0s;

               &:hover {
                  text-decoration: underline;
               }
            }
         }
      }
   }
}

.popup-title {
   font-size: 30px;
   line-height: 30 /30 * 100%;
   color: #303030;
}

.table {
   &::-webkit-scrollbar-track {
      border-radius: 10px;
   }

   max-height: 497px;
   overflow: auto;

   &::-webkit-scrollbar {
      width: 4px;
   }

   &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      height: 100px;
      background: #e5e5e5;
   }

   overflow: auto;
   font-size: 18px;
   line-height: 25 /18 * 100%;
   max-height: 600px;
   padding-right: 35px;

   &__row {
      display: flex;

      height: 40px;
      border-bottom: 1px solid #f2f3f7;

      &.active {
         & .table__column-day {
            border: 1px solid #ba0f08;
         }

         & .table__column-name {
            border: 1px solid #ba0f08;
         }
      }
   }

   &__row-head {
      border-bottom: 1px solid #2e8bc8;

      & .table__column-day {
         padding-left: 15px;
         background-color: #fff;
      }
   }

   &__head {
      font-weight: 600;
   }

   &__column-name {
      padding-left: 40px;
      font-size: 18px;
      line-height: 25 /18 * 100%;
      width: 20%;
      display: inline-flex;
      align-items: center;
   }

   &__column-day {
      width: 11.4%;
      background-color: #cfe4f2;
      display: inline-flex;
      align-items: center;

      &.not-work {
         background-color: #fff;
      }
   }
}

.popup-point {
   &__content {
      padding: 47px 32px 0 68px;
   }

   &__items {
      border-top: 1px solid #2e8bc8;
      overflow: auto;
      padding-right: 35px;

      &::-webkit-scrollbar-track {
         border-radius: 10px;
      }

      max-height: 497px;
      overflow: auto;

      &::-webkit-scrollbar {
         width: 4px;
      }

      &::-webkit-scrollbar-thumb {
         border-radius: 10px;
         height: 100px;
         background: #e5e5e5;
      }
   }

   &__item {
      font-family: "Roboto";

      border-bottom: 1px solid #c7c7c7;
      padding: 13px 0 21px;

      & span {
         font-size: 18px;
         font-weight: 400;
      }
   }

   &__city {
      font-size: 18px;
      position: relative;

      &._active::after {
         transform: rotate(0deg);
      }

      &::after,
      &::before {
         transition: all 0.3s ease 0s;
         position: absolute;
         top: 50%;
         transform: translateY(-50%);
         width: 20px;
         height: 2px;
         background-color: #303030;
         right: 0;
         content: "";
      }

      &::after {
         transform: rotate(90deg);
      }
   }

   &__info {
      margin-top: 15px;
   }

   &__icon-adress {
      width: 16px;
      height: 16px;
      margin-right: 6px;

      & svg {
         fill: #2e8bc8;
      }
   }

   &__adress {
      margin-bottom: 10px;
      display: flex;
      align-items: center;
   }

   &__phone {
      margin-bottom: 10px;
      display: inline-flex;
      align-items: center;
   }

   &__time {
      display: flex;
      align-items: center;
   }

   &__icon-phone {
      width: 16px;
      height: 16px;
      margin-right: 6px;

      & svg {
         fill: #2e8bc8;
      }
   }

   &__icon-time {
      width: 16px;
      height: 16px;
      margin-right: 6px;

      & svg {
         fill: #2e8bc8;
      }
   }

   &__list {
      margin-bottom: 15px;
   }

   &__time-wrap {
      display: flex;
      flex-direction: column;

      & span {
         margin-bottom: 5px;

         &:last-child {
            margin-bottom: 0;
         }
      }
   }
}

.popup-courier {
   &__item {
      flex: 0 0 50%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      &--big {
         flex: 0 0 100%;
      }
   }

   &__gift {
      margin-top: 15px;
      font-size: 18px;
      padding-left: 17px;
   }

   &__info {
      color: #ba0f08;
      margin-bottom: 15px;
   }

   &__body {
      max-width: 100%;

      &--small {
         max-width: 50%;
      }
   }

   &__content {
      padding: 47px 32px 25px 83px;
   }

   &__subtitle {
      font-size: 18px;
      line-height: 25 /18 * 100%;
      margin-bottom: 10px;
   }

   &__items {
      overflow: auto;

      &::-webkit-scrollbar-track {
         border-radius: 10px;
      }

      max-height: 497px;
      overflow: auto;

      &::-webkit-scrollbar {
         width: 4px;
      }

      &::-webkit-scrollbar-thumb {
         border-radius: 10px;
         height: 100px;
         background: #e5e5e5;
      }
   }

   &__row {
      display: flex;
      margin-bottom: 8px;

      &:first-child {
         & .popup-courier__item:first-child {
            border-right: 1px solid #2e8bc8;
         }
      }
   }
}

.popup-terminal .courier-item__cities {
   height: auto;
}

.popup-terminal .popup-courier__item {
   justify-content: flex-start;
}

.courier-item {
   &__cities {
      background: #f2f3f7;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      padding: 10px 10px 10px 17px;
      width: 100%;
      height: 100%;

      & span {
         font-weight: 600;
         font-size: 16px;
         line-height: 25 /18 * 100%;
         margin-right: 4px;
      }
   }

   &__head-row {
      border-bottom: 1px solid #2e8bc8;
      font-weight: 600;
      font-size: 18px;
      line-height: 25 /18 * 100%;
      padding: 7px 7px 8px;
      display: flex;
   }

   &__column {
      flex: 0 0 33.33%;
      display: inline-flex;
      justify-content: center;
      font-size: 18px;
      line-height: 25 /18 * 100%;
   }

   &__row {
      display: flex;
      padding: 9px 5px 4px;

      &:nth-child(even) {
         background: #fafafa;
      }
   }
}

@media (min-width: 991px) {
   .popup-point {
      &__item {
         align-items: center;
         display: flex;
      }

      &__city {
         flex: 0 0 16%;

         &::after,
         &::before {
            display: none;
         }
      }

      &__info {
         display: flex;
         align-items: center;
         justify-content: space-between;
         flex: 0 0 84%;
      }
   }
}

@media (max-width: 1110px) {
   .popup-point__item span {
      font-size: 14px;
   }
}

@media (max-width: 991px) {
   .popup-day__content {
      padding: 24px 20px 0 31px;
   }

   .popup-point__item {
      padding: 9px 0 13px;
   }

   .table__column-name {
      padding-left: 20px;
      font-size: 14px;
   }

   .table__row {
      height: 30px;
   }

   .popup-title {
      font-size: 25px;
   }

   .popup-day__list-city ul li a {
      font-size: 14px;
   }
}

@media (max-width: 800px) {
   .courier-item__cities span {
      font-size: 15px;
   }

   .courier-item__column {
      font-size: 16px;
   }
}

@media (max-width: 768px) {
   .popup-point__content {
      padding: 33px 13px 0 23px;
   }

   .popup-courier__content {
      padding: 30px 15px 15px 25px;
   }

   .popup-courier__row {
      flex-direction: column;
   }

   .popup-courier__row:last-child .popup-courier__body {
      max-width: 100%;
   }

   .popup-courier__item {
      margin-bottom: 15px;
   }
}

@media (max-width: 650px) {
   .table {
      font-size: 13px;
      padding-right: 5px;
   }

   .popup-courier__subtitle {
      font-size: 14px;
   }

   .popup-point__content {
      padding: 33px 13px 0 23px;
   }

   .popup__close {
      right: 10px;
      top: 1px;
   }

   .popup-title {
      font-size: 19px;
   }

   .table__column-name {
      padding-left: 10px;
      font-size: 12px;
   }

   .table__column-day {
      width: 9.4%;
   }

   .table__column-name {
      width: 34%;
   }

   .popup-day__content {
      padding: 34px 5px 0 5px;
   }

   .table__row-head .table__column-day {
      padding-left: 5px;
   }
}

@media (max-width: 550px) {
   .popup-point__city {
      font-size: 16px;
   }

   .popup-point__city::after,
   .popup-point__city::before {
      width: 15px;
      height: 1px;
   }
}
