.filter {
   padding: 28px 0 35px;

   &__head {
      display: flex;
      align-items: center;
      margin-bottom: 32px;
      padding-bottom: 12px;
      border-bottom: 1px solid #979797;
   }

   &__warn {
      margin: 30px 0 43px;
   }

   &__more {
      max-width: 216px;
      width: 100%;
      margin: 30px auto 0;

      & button {
         line-height: 55px;
      }
   }

   &__items {
      display: flex;
      flex-wrap: wrap;
      margin: -13px -10px 13px -10px;
      position: relative;
   }

   &__item {
      flex: 0 0 calc(25% - 20px);
      max-width: calc(25% - 20px);
      margin: 13px 10px;
   }
}

.filter-head {
   &__label {
      position: relative;
      padding-left: 33px;
      font-size: 18px;
      line-height: 24 /18 * 100%;

      &::after {
         position: absolute;
         content: "";
         top: 50%;
         left: 0;
         width: 27px;
         height: 20px;
         background-repeat: no-repeat;
         background-image: url("../img/filter.png");
         background-size: contain;
         transform: translateY(-50%);
      }
   }

   &__selects {
      margin-left: auto;
      display: flex;
      align-items: center;
   }

   &__select {
      font-size: 18px;
      margin-right: 85px;

      &:last-child {
         margin-right: 0;
      }
   }
}

.card--filter {
   padding: 25px;
}

.filter {
   & .card {
      transition: all 0.3s ease 0s;

      &:hover {
         box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.15);
         transform: translateY(-12px);
      }

      &__slider-dots {
         display: flex;
         position: absolute;
         bottom: 0;
         left: 50%;
         transform: translateX(-50%);

         & li {
            &.slick-active {
               background: #a7daea;
            }

            cursor: pointer;
            border-radius: 50%;
            background: #e5e5e5;
            font-size: 0;
            width: 18px;
            height: 18px;
            margin-right: 7px;

            &:last-child {
               margin-right: 0;
            }
         }
      }

      &__sale {
         position: absolute;
         width: 45px;
         height: 45px;
         display: inline-flex;
         align-items: center;
         justify-content: center;
         top: 15px;
         left: 25px;
         font-weight: bold;
         font-size: 22px;
         color: #ffffff;
         background: #ba0f08;
         border-radius: 50%;
      }

      &__slider {
         max-height: 200px;
         margin-bottom: 5px;
         padding-bottom: 5px;
      }

      &__old-price {
         font-size: 18px;
         font-weight: 400;
         text-decoration: line-through;
         margin-left: 10px;
      }
   }
}

.buttons-buy__one-click {
   white-space: nowrap;
}

@media (max-width: 1200px) {
   .quick.fixed {
      position: fixed;
      width: 100%;
      height: 100%;
      left: 0;
      overflow: auto;
      top: 0;
      z-index: 1000;
   }

   .filter__item {
      flex: 0 0 calc(33.33% - 20px);
      max-width: calc(33.33% - 20px);
   }
}

@media (max-width: 938px) {
   .filter__item {
      flex: 0 0 calc(50% - 20px);
      max-width: calc(50% - 20px);
   }
}

@media (max-width: 768px) {
   .filter-head__select {
      font-size: 16px;
      margin-right: 15px;
   }

   .select__current {
      font-size: 16px;
   }

   .filter-head__label {
      font-size: 16px;
      margin-right: 20px;
   }

   .filter__warn {
      margin: 15px 0 30px;
   }
}

@media (max-width: 670px) {
   .filter__item {
      flex: 0 0 calc(100% - 20px);
      max-width: calc(100% - 20px);
   }

   .filter__head {
      justify-content: space-between;
   }

   .filter-head__label {
      margin-right: 40px;
   }

   .filter {
      padding: 19px 0 14px;
   }

   .filter-head__select {
      margin-bottom: 10px;
   }

   .filter-head__selects {
      flex-wrap: wrap;
   }

   .card__img {
      max-width: 300px;
      padding: 0 0 31%;
   }
}

@media (max-width: 550px) {
   .card__img {
      max-width: 80%;
      padding: 0 0 35%;
   }
}

@media (max-width: 400px) {
   .card__img {
      padding: 0 0 50%;
   }
}
