.features {
   padding: 72px 0;

   &__title {
      text-align: center;
      margin-bottom: 35px;
   }

   &__items {
      display: flex;
      flex-wrap: wrap;
      margin: -12px -13px;
   }

   &__item {
      flex: 0 0 calc(50% - 26px);
      margin: 12px 13px;
      display: flex;
      background: #ffffff;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
      border-radius: 10px;
   }

   &__content {
      margin-right: 25px;
      padding: 33px 5px 20px 33px;
   }

   &__name {
      font-size: 24px;
      line-height: 118.5%;
      color: #ba0f08;
      margin-bottom: 15px;
   }

   &__text {
      font-size: 18px;
      line-height: 25 /18 * 100%;
      max-width: 270px;
   }

   &__img {
      min-height: 150px;
      padding-bottom: 40%;
      width: 100%;
      max-width: 300px;

      & img {
         object-fit: contain;
      }
   }
}

@media (max-width: 991px) {
   .features__item {
      flex-direction: column;
      justify-content: space-between;
      padding: 20px;
   }

   .features {
      padding: 50px 0;
   }

   .features__content {
      margin-right: 0;
      margin-bottom: 20px;
      padding: 0;
   }

   .features__img {
      padding-bottom: 32%;
      margin: 0 auto;
      max-width: 340px;
   }

   .features__text {
      max-width: 100%;
   }
}

@media (max-width: 768px) {
   .features__text {
      font-size: 16px;
   }

   .features__item {
      padding: 15px;
   }

   .features__name {
      font-size: 20px;
      margin-bottom: 10px;
   }

   .features__img {
      min-height: 110px;
   }
}

@media (max-width: 550px) {
   .features__item {
      flex: 0 0 calc(100% - 26px);
   }

   .features {
      padding: 30px 0;
   }
}
