.catalog-top {
   padding: 47px 0 0;

   &__wrapper {
      text-align: center;
   }

   &__title {
      font-weight: 600;
      font-size: 40px;
      line-height: 45 /40 * 100%;
   }

   &__city {
      font-size: 18px;
      line-height: 24 /18 * 100%;
      max-width: 488px;
      margin: 5px auto 31px;
   }
}

@media (max-width: 991px) {
   .payment__title {
      font-weight: 600;
      font-size: 27px;
      margin-bottom: 16px;
   }

   .catalog-top {
      padding: 35px 0 0;
   }

   .payment__body {
      padding: 30px 10px 16px;
   }
}

@media (max-width: 768px) {
   .catalog-top__title {
      font-size: 33px;
   }

   .payment__title {
      font-size: 22px;
   }
}

@media (max-width: 550px) {
   .catalog-top {
      padding: 20px 0 0;
   }

   .catalog-top__city {
      font-size: 14px;
      margin: 5px auto 22px;
   }

   .catalog-top__title {
      font-size: 26px;
   }

   .payment__title {
      font-size: 18px;
      margin-bottom: 10px;
   }
}
