.banner {
   padding: 18px 0;

   &__wrapper {
      min-height: 500px;
      display: flex;
      justify-content: flex-end;
   }

   &__body {
      padding: 64px 20px 80px 48px;
      max-width: 955px;
      width: 100%;
      background: #cfe4f2;
      border-radius: 10px;
      display: flex;
      transition: all 0.4s ease 0s;

      &.scroll {
         justify-content: space-around;
         max-width: 100%;
         margin-left: 0;
      }
   }

   &__content {
      width: 100%;
      max-width: 420px;
   }

   &__title {
      font-weight: 600;
      font-size: 40px;
      line-height: 45 /40 * 100%;
      margin-bottom: 30px;
   }

   &__descr {
      font-size: 18px;
      line-height: 24 /18 * 100%;
      margin-bottom: 40px;
   }

   &__pay {
      padding: 15px 15px 22px 22px;
      background: #98c5e2;
      border-radius: 10px;
      max-width: 374px;
      font-size: 22px;
      line-height: 26 /22 * 100%;
      margin-bottom: 23px;
   }

   &__img {
      position: relative;
      min-height: 200px;
      left: -20px;
      max-width: 488px;
      width: 100%;
      padding: 0px 0px 37% 0px;

      & img {
         object-fit: contain;
      }
   }
}

@media (max-width: 1300px) {
   .banner__body {
      flex: 1 1 auto;
      margin-left: 325px;
   }

   .banner__pay {
      max-width: 354px;
      font-size: 20px;
      margin-bottom: 19px;
   }
}

@media (max-width: 1195px) {
   .banner__body {
      padding: 30px 15px 40px 25px;
      flex-wrap: wrap;
      justify-content: center;
   }

   .banner__img {
      left: 0;
      margin-top: 20px;
   }

   .banner__btn-wrap {
      display: flex;
      align-items: center;
   }

   .banner__pay {
      margin-right: 20px;
   }

   .btn-dotted {
      font-size: 16px;
   }

   .banner__content {
      display: flex;
      max-width: 100%;
      flex-wrap: wrap;
   }

   .banner__title {
      font-size: 34px;

      margin-bottom: 24px;
   }
}

@media (max-width: 768px) {
   .page {
      margin-top: 88px;
   }

   .banner__descr {
      font-size: 16px;

      margin-bottom: 25px;
   }

   .banner__title {
      font-size: 28px;
      margin-bottom: 19px;
   }

   .banner__pay {
      max-width: 454px;
      font-size: 18px;
      margin-bottom: 17px;
   }

   .banner__body {
      margin-left: 0;
   }
}

@media (max-width: 550px) {
   .page {
      margin-top: 70px;
   }
}

@media (max-width: 400px) {
   .btn-dotted {
      font-size: 12px;
   }

   .banner__pay {
      font-size: 16px;
   }
}
