.price {
   padding: 36px 0 28px;

   &__title {
      text-align: center;
      margin-bottom: 14px;
   }

   &__subtitle {
      text-align: center;
      max-width: 686px;
      margin: 0 auto 40px;
   }

   &__slider-wrapper {
      position: relative;
   }

   &__show-all {
      margin: 13px auto 0;
      max-width: 216px;
      width: 100%;
   }
}

.price-slider {
   &__slide {
      margin: 0 10px;
      padding: 20px 0;
   }

   &__left {
      left: -20px;
   }

   &__right {
      right: -20px;
   }

   &__left,
   &__right {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
   }
}

.price {
   & .card {
      padding: 34px 32px 35px;
      text-align: center;

      &__img {
         padding: 0 0 58%;
         max-width: 300px;

         & img {
            object-fit: contain;
         }
      }

      &__buttons {
         margin: 0 auto 10px;
      }
   }
}

@media (max-width: 991px) {
   .price .card {
      padding: 20px 15px 20px;
   }
}

@media (max-width: 800px) {
   .price .buttons-buy__one-click {
      font-size: 14px;
      padding: 0 70px 0 15px;
   }

   .price__subtitle {
      margin: 0 auto 22px;
   }
}

@media (max-width: 768px) {
   .price__subtitle {
      margin: 0 auto 11px;
   }
}
