.how-work {
   padding: 52px 0 35px;

   &__title {
      margin-bottom: 47px;
      text-align: center;
   }

   &__items {
      display: flex;
      justify-content: space-between;
   }

   &__item {
      max-width: 300px;
      width: 100%;
      text-align: center;
      position: relative;

      &::after {
         position: absolute;
         content: "";
         width: 36px;
         height: 17px;
         top: 20px;
         right: -30px;
         background-repeat: no-repeat;
         background-image: url("../img/sprite/arrow-long.svg");
      }

      &:last-child::after {
         display: none;
      }
   }

   &__icon {
      width: 58px;
      height: 58px;
      margin-bottom: 13px;

      & svg {
         stroke: #ba0f08;
      }
   }

   &__item-title {
      font-weight: 600;
      font-size: 20px;
      margin-bottom: 15px;
   }

   &__descr {
      font-size: 18px;
      line-height: 20 /18 * 100%;
   }
}

@media (max-width: 768px) {
   .how-work {
      padding: 30px 0 24px;
   }

   .how-work__icon {
      width: 49px;
      height: 43px;
      margin-bottom: 7px;
   }

   .how-work__item-title {
      font-size: 20px;
      margin-bottom: 8px;
   }

   .how-work__descr {
      font-size: 16px;
   }

   .how-work__items {
      flex-wrap: wrap;
   }

   .how-work__item {
      max-width: 100%;
      flex: 0 0 45%;
      margin-bottom: 20px;
   }

   .how-work__item::after {
      display: none;
   }
}

@media (max-width: 550px) {
   .how-work__item {
      flex: 0 0 100%;
   }

   .how-work__item-title {
      font-size: 18px;
   }
}
