.return {
   padding: 44px 0 28px;

   &__wrapper {
      background: #f2f3f7;
      border-radius: 15px;
      padding: 44px 44px 26px 44px;
   }

   &__items {
      display: flex;
      justify-content: space-between;
   }

   &__item {
      display: flex;
   }

   &__icon {
      width: 66px;
      height: 66px;
      margin-right: 15px;

      & svg {
         fill: #ba0f08;
         stroke: #ba0f08;
      }
   }

   &__content {
      max-width: 480px;
   }

   &__title {
      font-weight: bold;
      font-size: 36px;
      line-height: 40 /36 * 100%;
      color: #4b4b4b;
      margin-bottom: 6px;
   }

   &__info {
      font-size: 18px;
      line-height: 25 /18 * 100%;
      color: #4b4b4b;
   }
}

@media (max-width: 1240px) {
   .return__item {
      flex: 0 0 48%;
   }

   .return__icon {
      flex: 0 0 66px;
   }
}

@media (max-width: 1060px) {
   .return__icon {
      flex: 0 0 50px;
      height: 50px;
      margin-right: 10px;
   }

   .return__title {
      font-size: 33px;
   }
}

@media (max-width: 991px) {
   .return__item {
      flex: 0 0 100%;
      margin-bottom: 20px;
   }

   .return__content {
      max-width: 100%;
   }

   .return__items {
      flex-wrap: wrap;
   }
}

@media (max-width: 768px) {
   .return__wrapper {
      padding: 21px 20px 20px;
   }

   .return {
      padding: 20px 0 20px;
   }

   .return__title {
      font-size: 22px;
   }

   .return__icon {
      flex: 0 0 30px;
      height: 30px;
   }

   .return__info {
      font-size: 16px;
   }
}
