.type {
   padding: 53px 0;

   &__head {
      margin-bottom: 42px;
      text-align: center;
   }

   &__title {
      margin-bottom: 13px;
   }

   &__subtitle {
      max-width: 686px;
      margin: 0 auto 7px;
   }

   &__items {
      display: flex;
      flex-wrap: wrap;
      margin: -12.5px -10px;
   }

   &__item {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      margin: 12.5px 10px;
      flex: 0 0 calc(25% - 20px);
      padding: 12px 40px 40px 40px;
      background: #ffffff;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
      border-radius: 10px;
   }
}

.type-item {
   transition: all 0.3s ease 0s;
   position: relative;

   &__img {
      max-width: 215px;
      padding-bottom: 56%;
      width: 100%;
      min-height: 70px;

      & img {
         object-fit: contain;
      }
   }

   &:hover {
      box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
      transform: translateY(-20px);
      z-index: 4;

      & .type-item__hide {
         visibility: visible;
         opacity: 1;
         pointer-events: all;
      }
   }

   &__name {
      margin-top: 25px;
      font-size: 18px;
      line-height: 24 /18 * 100%;
      margin-bottom: 15px;
   }

   &__hide {
      width: 100%;
      position: absolute;
      top: calc(100% - 30px);
      left: 50%;
      transform: translateX(-50%);
      visibility: hidden;
      z-index: 2;
      border-top-width: 0;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s ease 0s;
      background: #ffffff;
      box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.05);
      border-radius: 10px;
      padding-bottom: 40px;
   }

   &__bottom {
      text-align: center;
   }

   &__all {
      max-width: 196px;
      margin-bottom: 13px;
   }
}

@media (max-width: 991px) {
   .type__item {
      flex: 0 0 calc(33.33% - 20px);
   }

   .type {
      padding: 30px 0;
   }
}

@media (max-width: 768px) {
   .type-item {
      padding: 12px 20px 20px;
      flex: 0 0 calc(50% - 20px);
   }

   .type-item__hide {
      width: 100%;
      position: relative;
      top: 0;
      left: unset;
      opacity: 1;
      visibility: visible;
      right: unset;
      pointer-events: all;
      transform: translateX(0px);
      background: #fff;
      box-shadow: none;
   }
}

@media (max-width: 550px) {
   .type-item {
      flex: 0 0 calc(100% - 20px);
   }

   .type-item__img {
      max-width: 305px;
   }

   .type__title {
      margin-bottom: 8px;
   }

   .btn-link {
      font-size: 14px;
   }

   .subtitle {
      font-size: 16px;
   }

   .type-item__hide {
      display: flex;
      flex-direction: column;
      align-items: center;
      top: calc(100% - 14px);
   }
}
